class Popup {
	constructor() {
		this.stack = [];
		this.count = 0;
		this.zIndex = 2000;
	}
	nextZIndex() {
		this.zIndex += 1;
		return this.zIndex;
	}
	add() {
		this.count += 1;
	}
	remove() {
		this.count -= 1;
		this.count = this.count >= 0 ? this.count : 0;
	}
	size() {
		return this.count;
	}
}

const popup = new Popup();

export default {
	popup,
};
