<template>
  <div :class="classes">
    <slot>
      <span :class="['ac-sprite__icon__title']">{{ title }}</span>
      <ac-action v-if="icon">
        <ac-action-block @click.native="onClick">
          <ac-icon :type="icon.type" ></ac-icon>
        </ac-action-block>
      </ac-action>
    </slot>
  </div>
</template>

<script>
import { globalAssistService } from '$src/utils';

export default {
  name: 'AcSpriteIcon',
  props: {
    title: String,
    icon: Object,
    type: {
      default: 'info',
      validator(value) {
        return globalAssistService.validate.oneOf(value, ['info', 'success', 'warning', 'error', 'default']);
      },
    },
  },
  computed: {
    classes() {
      return [
        'ac-layout__flex',
        'ac-sprite__icon',
        'ac-layout',
        'ac-layout--center-items',
        'ac-layout--center-justify',
        'ac-layout--column',
        {
          [`ac-sprite__icon--${this.type}`]: !!this.type,
        },
      ];
    },
  },
  methods: {
    onClick() {
      this.$emit('on-click');
    },
  },
};
</script>
