<template>
  <div :class="['ac-table-pagination', 'ac-layout', 'ac-layout--end-justify']">
    <Page
      :class="['ac-layout__flex-none']"
      :current="current"
      :total="total"
      :page-size="pageSize"
      :page-size-opts="pageSizeOpts"
      :placement="placement"
      size="small"
      :show-total="true"
      :show-sizer="true"
      @on-change="onChange"
      @on-page-size-change="onSizeChange"
    ></Page>
  </div>
</template>

<script>
export default {
  name: 'AcTablePagination',
  props: {
    current: {
      type: Number,
      required: true,
    },
    total: {
      type: Number,
      required: true,
    },
    pageSize: {
      type: Number,
      required: true,
    },
    pageSizeOpts: {
      type: Array,
      required: true,
    },
    placement: {
      type: String,
      required: true,
    },
  },
  inject: ['claimChangePagination'],
  methods: {
    onChange(pageNum) {
      this.claimChangePagination('page-number', pageNum);
    },
    onSizeChange(pageSize) {
      this.claimChangePagination('page-size', pageSize);
    },
  },
};
</script>
