<template>
<div :class="[`${cssPrefix}`, 'ac-layout', 'ac-layout--column']"
  :style="wrapperStyle"
>
  <ac-table-header
    ref="header"
    v-if="showHeader"
    v-bind="headerProps"
    :class="['ac-layout__flex-none']"
  ></ac-table-header>
  <ac-table-body
    ref="body"
    v-bind="bodyProps"
    :class="['ac-layout__flex']"
  ></ac-table-body>
</div>
</template>

<script>
import AcTableHeader from './ac-table-header';
import AcTableBody from './ac-table-body';
import utilService from './util.service';

export default {
  name: 'AcTableFixedPortion',
  props: {
    /** `left` or `right` */
    side: {
      type: String,
      required: true,
    },
    portionColumnsConfig: {
      type: Array,
      required: true,
    },
    showHeader: {
      type: Boolean,
      required: true,
    },
    headerMetrics: {
      type: Object,
      required: true,
    },
    originalHeaderProps: {
      type: Object,
      required: true,
    },
    originalBodyProps: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      cssPrefix: 'ac-table-fixed-portion',
    };
  },
  computed: {
    /** we need to recalculate the layeredColumnsConfig for fixed portion */
    layeredColumnsConfig() {
      /** XXX */
      return [this.portionColumnsConfig.map(col => ({ colspan: 1, rowspan: 1, col }))];
    },
    /** we need to recalculate the effectiveColumnsConfig for fixed portion */
    effectiveColumnsConfig() {
      return this.portionColumnsConfig.slice(0);
    },
    headerProps() {
      return Object.assign({}, this.originalHeaderProps, {
        columnsConfig: this.portionColumnsConfig,
        layeredColumnsConfig: this.layeredColumnsConfig,
        effectiveColumnsConfig: this.effectiveColumnsConfig,
        forFixedPortion: true,
      });
    },
    bodyProps() {
      return Object.assign({}, this.originalBodyProps, {
        columnsConfig: this.portionColumnsConfig,
        effectiveColumnsConfig: this.effectiveColumnsConfig,
        forFixedPortion: this.side,
      });
    },
    wrapperStyle() {
      const width = utilService.calcTableWidth(this.effectiveColumnsConfig);
      return {
        width: `${width}px`,
        height: `${this.originalHeaderProps.bodyMetrics.clientHeight + (this.showHeader ? this.headerMetrics.clientHeight : 0)}px`,
      };
    },
  },
  methods: {
    updateBodyPosition(param) {
      this.$refs.body.updatePosition(param);
    },
  },
  components: {
    AcTableHeader,
    AcTableBody,
  },
};
</script>
