/* eslint guard-for-in: 0 */
/* eslint no-restricted-syntax: 0 */
/* eslint no-useless-return: 0 */
/* eslint no-param-reassign: 0 */
/* eslint-disable no-bitwise */
import _ from 'lodash';
import validateService from './validate.service';

function evil(fn) {
  const Fn = Function;
  return new Fn(`return ${fn}`)();
}

function deepCopy(data) {
  const t = validateService.typeOf(data);
  let o;

  if (t === 'array') {
    o = [];
  } else if (t === 'object') {
    o = {};
  } else {
    return data;
  }

  if (t === 'array') {
    for (let i = 0; i < data.length; i++) {
      o.push(deepCopy(data[i]));
    }
  } else if (t === 'object') {
    for (const i in data) {
      o[i] = deepCopy(data[i]);
    }
  }
  return o;
}

function mergeKey(sourceKey = [], targetKey = [], retain = false) {
  const newKeys = [];

  let keys = _.map([...sourceKey, ...targetKey], 'key');
  keys = _.uniq(keys);

  _.forEach(keys, (key) => {
    let [sourceChildren, targetChildren] = [
      [],
      [],
    ];

    _.forEach(sourceKey, (sourceItem) => {
      if (sourceItem.key === key) {
        sourceChildren = sourceItem.children;
        return;
      }
    });

    _.forEach(targetKey, (targetItem) => {
      if (targetItem.key === key) {
        targetChildren = targetItem.children;
        return;
      }
    });

    if (retain) {
      newKeys.push({
        key,
        children: [...sourceChildren, ...targetChildren],
        childrenS: [...sourceChildren],
        childrenT: [...targetChildren],
      });
    } else {
      newKeys.push({
        key,
        children: [...sourceChildren, ...targetChildren],
      });
    }
  });

  return newKeys;
}

function uniqKey(sourceKey = [], targetKey = []) {
  const newKeys = [];

  _.forEach(mergeKey(sourceKey, targetKey, true), (item) => {
    const children = _.xor(item.childrenS, item.childrenT);
    if (children.length > 0) {
      newKeys.push({
        key: item.key,
        children,
      });
    }
  });

  return newKeys;
}

function separateData(dataSource = [], targetKey = []) {
  const [leftData, rightData] = [
    [],
    [],
  ];

  _.forEach(dataSource, (item) => {
    let leftItem; let rightItem;
    const itemLegal = _.reject(targetKey, keyItem => keyItem.key !== item.key);

    if (itemLegal.length > 0) {
      leftItem = _.omit(item, 'children');
      rightItem = _.omit(item, 'children');

      leftItem.children = _.reject(item.children, subItem => itemLegal[0].children.indexOf(subItem.key) > -1);
      rightItem.children = _.reject(item.children, subItem => itemLegal[0].children.indexOf(subItem.key) <= -1);
    } else {
      leftItem = item;
    }

    if (leftItem && leftItem.children.length > 0) leftData.push(leftItem);
    if (rightItem && rightItem.children.length > 0) rightData.push(rightItem);
  });

  return [leftData, rightData];
}

function filterKey(data, key, concat) {
  let a = '';
  if (validateService.typeOf(key) === 'array') {
    const b = [];
    _.forEach(key, (item) => {
      b.push(data[item]);
    });
    a = b.join(concat);
  } else {
    a = data[key];
  }
  return a;
}

function formatTransferData(data = [], obj = {}) {
  const newData = [];
  if (data.length === 0) return [];
  let defaultObj = {
    hasChildren: false,
    childrenField: '',
    concat: '-',
    key: [],
    label: [],
    disabled: () => false,
    children: {
      concat: '-',
      key: [],
      label: [],
      disabled: () => false,
    },
  };
  defaultObj = _.assign(defaultObj, obj);
  if (!defaultObj.children.concat) {
    defaultObj.children.concat = '-';
  }
  if (!defaultObj.children.disabled) {
    defaultObj.children.disabled = () => false;
  }

  if (defaultObj.hasChildren) {
    _.forEach(data, (item) => {
      const a = {
        key: filterKey(item, defaultObj.key, defaultObj.concat),
        label: filterKey(item, defaultObj.label, defaultObj.concat),
        disabled: defaultObj.disabled(item),
        children: [],
      };
      _.forEach(item[defaultObj.childrenField], (subItem) => {
        a.children.push({
          key: filterKey(subItem, defaultObj.children.key, defaultObj.children.concat),
          label: filterKey(subItem, defaultObj.children.label, defaultObj.children.concat),
          disabled: defaultObj.children.disabled(item),
        });
      });
      newData.push(a);
    });
  } else {
    _.forEach(data, (item) => {
      const key = filterKey(item, defaultObj.key, defaultObj.concat);
      const label = filterKey(item, defaultObj.label, defaultObj.concat);
      const disabled = defaultObj.disabled(item);
      newData.push({
        key,
        label,
        disabled,
      });
    });
  }
  return newData;
}

function formatTableData(data, key, retainKeyProp) {
  const t = validateService.typeOf(data);
  let o;

  if (t === 'array') {
    o = [];
  } else if (t === 'object') {
    o = {};
  } else {
    return data;
  }

  if (t === 'array') {
    for (let i = 0; i < data.length; i++) {
      o.push(formatTableData(data[i], key, retainKeyProp));
    }
  } else if (t === 'object') {
    for (const i in data) {
      if (i === key) {
        o._key = formatTableData(data[i], key, retainKeyProp);
        if (retainKeyProp) {
          o[i] = formatTableData(data[i], key, retainKeyProp);
        }
      } else if (validateService.typeOf(data[i]) === 'array') {
        o._children = formatTableData(data[i], key, retainKeyProp);
      } else {
        o[i] = formatTableData(data[i], key, retainKeyProp);
      }
    }
  }
  return o;
}

function formatParam(data = [], params = [], sub, split = ',') {
  if (data.length === 0 || params.length === 0) return data;
  _.forEach(params, (item, index) => {
    if (index === 0) {
      _.forEach(data, (itm) => {
        if (item !== null) {
          itm._params = itm[item];
        }
      });
    } else if (index === 1) {
      _.forEach(data, (itm) => {
        _.forEach(itm[sub], (itm1) => {
          if (item !== null) {
            itm1._params = `${itm._params ? itm._params + split : ''}${itm1[item]}`;
          }
        });
      });
    } else if (index === 2) {
      _.forEach(data, (itm) => {
        _.forEach(itm[sub], (itm1) => {
          _.forEach(itm1[sub], (itm2) => {
            if (item !== null) {
              itm2._params = `${itm._params ? itm._params + split : ''}${itm1._params ? itm1._params + split : ''}${split}${itm1[item]}`;
            }
          });
        });
      });
    }
  });
  return data;
}

function numberToIp(ipNumber) {
  let ip = '';
  if (ipNumber || ipNumber > 0) {
    ip = `${((ipNumber << 0) >>> 24)}.${((ipNumber << 8) >>> 24)}.${((ipNumber << 16) >>> 24)}.${((ipNumber << 24) >>> 24)}`;
  }
  return ip;
}

function ipToNumber(ip) {
  let num = 0;
  ip = ip.split('.');
  num = (Number(ip[0]) * 256 * 256 * 256) + (Number(ip[1]) * 256 * 256) + (Number(ip[2]) * 256) + Number(ip[3]);
  [num] = [num >>> 0];
  return num;
}

/**
 * use for amchart.
 * format the amchat unit
 *
 * @param {String} unit the origin unit
 * @param {Number} type format type
 * @returns
 * if unit not exsit, or unit === 个|pc|pc. return '';
 * else if type === 0 , return the origin unit,
 * if type === 1, return unit surronded by (),
 */
function formatAmchartUnit(unit, type = 0) {
  let result = '';
  if (unit) {
    if (unit !== '个' || unit !== 'pc' || unit !== 'pc.') {
      if (type === 0) {
        result = unit;
      } else if (type === 1) {
        result = `(${unit})`;
      }
    }
  }
  return result;
}


/**
 * format the date expression in origin data to real date;
 * @param {Array} sourceData the origin data, it may like this:
 * [
 *  {
 *    num: ...,
 *    alarm: ...,
 *    utc: 'new Date(2018, 2, 13, 08, 33)'
 *  },
 *  {
 *    num: ...,
 *    alarm: ...,
 *    utc: 'new Date(2018, 2, 13, 08, 34)'
 *  },
 *  ...
 * ]
 * @return {Array} formatedData, data after format may like this:
 * [
 *  {
 *    num: ...,
 *    alarm: ...,
 *    utc: Tue Mar 13 2018 08:33:00 GMT+0800 (中国标准时间)
 *  },
 *  {
 *    num: ...,
 *    alarm: ...,
 *    utc: Tue Mar 13 2018 08:34:00 GMT+0800 (中国标准时间)
 *  },
 * ...:
 * ]
 *
 * if sourceData not exist, return [];
 */
function formatAmchartDate(sourceData) {
  let formatedData = [];
  if (sourceData && sourceData.length) {
    formatedData = evil(JSON.stringify(sourceData).replace(/"new/g, 'new').replace(/\)"/g, ')'));
  }
  return formatedData;
}

/**
 * Input: {key: ['title', 'unit'], key2: ['title2', 'unit2'] ...}
 * Output: [{key: 'col0', datakey: key, title: 'title+unit'} ...]
 * e.g.
 * { time: ['时间','天']}
 * =>
 * [key: 'col0', dataKey:'time', title: '时间(天)']
 *
 * @param {Object} sourceData
 * @return {Array}
 */
function formatAmchartColumn(sourceData) {
  let returnVal = [];

  if (sourceData && Object.keys(sourceData).length) {
    const tempData = sourceData;
    returnVal = Object.keys(tempData).map((key, index) => {
      const tempVal = tempData[key];
      const data = {
        key: `col${index}`,
        dataKey: key,
        title: `${tempVal[0] || ''}${formatAmchartUnit(tempVal[1]) || ''}`,
      };
      return data;
    });
  }

  return returnVal;
}

export default {
  deepCopy,
  evil,
  mergeKey,
  uniqKey,
  separateData,
  filterKey,
  formatTransferData,
  formatTableData,
  formatParam,
  numberToIp,
  ipToNumber,
  formatAmchartUnit,
  formatAmchartDate,
  formatAmchartColumn,
};
