<script>
import { AcLoading } from '../ac-loading';

export default {
  name: 'AcTableRowExpanded',
  props: {
    columnsConfig: {
      type: Array,
      required: true,
    },
    effectiveColumnsConfig: {
      type: Array,
      required: true,
    },
    effectivePaginationConfig: {
      type: Object,
    },
    entry: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
      required: true,
    },
    accordion: {
      type: Boolean,
      required: true,
    },
    size: {
      type: String,
      required: true,
    },
    stripe: {
      type: Boolean,
      required: true,
    },
    border: {
      type: Boolean,
      required: true,
    },
    nestLevel: {
      type: Number,
      required: true,
    },
    indentSize: {
      type: Number,
      required: true,
    },
  },
  inject: ['claimNestedEntryExpanded'],
  computed: {
    expandColConfig() {
      return this.effectiveColumnsConfig.find(col => col.type === 'expand');
    },
  },
  methods: {
    onNestedEntryExpanding(params) {
      this.claimNestedEntryExpanded(params);
    },
  },
  beforeCreate() {
    /* eslint-disable global-require */
    this.$options.components.AcTable = require('./ac-table').default;
  },
  render() {
    const contentNodes = [];

    if (this.expandColConfig) {
      /** regular expandable row, the render function must exist */
      let contentNode = null;
      if (this.entry._toLoadChildren === true) {
        /** we are doing lazy loading for this entry now */
        contentNode = (
          <ac-loading tip="加载中..."></ac-loading>
        );
      } else {
        contentNode = this.expandColConfig.render.call(this, this.$createElement, {
          entry: this.entry,
          index: this.index,
        });
      }
      contentNodes.push(contentNode);
    } else if (Array.isArray(this.entry._children)) {
      /** expandable row for tree table, so we will be composing the ac-table for next tree level in this expandable row */
      const nestedPaginationConfig = this.effectivePaginationConfig && this.effectivePaginationConfig.nestedPagination ?
        Object.assign({}, this.effectivePaginationConfig.nestedPagination) : null;
      let tableNode = null;

      if (this.entry._toLoadChildren === true) {
        /** we are doing lazy loading for this children rows now */
        tableNode = (
          <ac-loading tip="加载中..."></ac-loading>
        );
      } else {
        if (nestedPaginationConfig) {
          nestedPaginationConfig.total = (this.entry._childTotal !== undefined) ? this.entry._childTotal : this.entry._children.length;
        }
        tableNode = (
          <ac-table
            columnsConfig={this.columnsConfig}
            data={this.entry._children}
            accordion={this.accordion}
            size={this.size}
            stripe={this.stripe}
            border={this.border}
            showHeader={false}
            nestLevel={this.nestLevel + 1}
            indentSize={this.indentSize}
            paginationConfig={nestedPaginationConfig}
            {...{ on: { 'on-entry-expanding': this.onNestedEntryExpanding } }}
          ></ac-table>
        );
      }

      contentNodes.push(tableNode);
    }

    const rootNode = (
      <tr class={['ac-table-row-expanded']}>
        <td class={['ac-table-row-expanded__cell']}
          colspan={this.effectiveColumnsConfig.length}
        >
          <div class={['ac-table-row-expanded__cont-wrapper']}>
            {contentNodes}
          </div>
        </td>
      </tr>
    );

    return rootNode;
  },
  components: {
    AcLoading,
  },
};
</script>
