<template>
  <transition :name="transitionName" @after-leave="destroyElement">
    <div
      class="ac-modal-wrap ac-layout ac-layout--center-items ac-layout--center-justify"
      v-show="modalShow"
      :style="{ 'z-index': actualZIndex }"
      @click.self="onMask"
    >
      <div
        v-show="modalMainShowDelay"
        v-drag
        :class="[
          'ac-modal',
          'ac-layout',
          'ac-layout--column',
          { 'ac-modal--absolute-position': isDrag },
        ]"
        :style="[{width: modalWidth, height: modalHeight, maxWidth: modalMaxWidth, maxHeight: modalMaxHeight }, modalPositionStyle]"
      >
        <div
          ref="header"
          v-if="isHeader"
          :class="['ac-modal__header', 'ac-layout__flex-none', 'ac-layout', { 'ac-modal__header--drag': drag }]"
        >
          <slot name="header">
            <div class="ac-modal__header__title ac-layout ac-layout--center-items">
              <slot name="title">标题未定义</slot>
            </div>
            <div class="ac-layout__flex ac-moadl__header--filling"></div>
            <template v-if="closable">
              <ac-action>
                <ac-action-block>
                  <ac-icon type="close" @click.native="onClose"></ac-icon>
                </ac-action-block>
              </ac-action>
            </template>
          </slot>
        </div>

        <ac-scroll-view
          class="ac-modal__body ac-layout__flex"
          :visible="modalShow"
        >
          <div :class="['ac-layout', 'ac-modal__body__inner', 'ac-modal__body__inner--' + bodyPadding + '-padding' ]">
            <div class="ac-layout__flex"><div ref="noHeightView"></div><div><slot></slot></div></div>
            <div ref="noWidthView"></div>
          </div>
        </ac-scroll-view>

        <div v-if="isFooter" class="ac-modal__footer ac-layout__flex-none ac-layout" ref="footer">
          <slot name="footer">
            <div :class="['ac-layout__flex']"></div>
            <ac-action>
              <ac-action-block v-if="cancelable">
                <ac-button :type="cancelButtonType" size="small" @click.native="onCancel">{{ cancelButtonText }}</ac-button>
              </ac-action-block>
              <ac-action-block v-if="okable">
                <ac-button :type="okButtonType" size="small" :loading="buttonLoading" @click.native="onOk">{{ okButtonText }}</ac-button>
              </ac-action-block>
            </ac-action>
          </slot>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { globalAssistService } from '$src/utils';
import { AcScrollView } from '../ac-scroll-view';
import { AcIcon } from '../ac-icon';
import { AcAction, AcActionBlock } from '../ac-action';
import { AcButton } from '../ac-button';
import { DragDir } from './ac-modal-drag.dir';
import utilService from './util.service';

const DEFAULT_SIZE = {
  small: {
    width: 280,
    height: 130,
  },
  primary: {
    width: 400,
    height: 260,
  },
  large: {
    width: 880,
    height: 560,
  },
};

const DEFAULT_WINDOW_PADDING = 20;

export default {
  name: 'AcModal',
  directives: { drag: DragDir },
  components: {
    AcIcon,
    AcButton,
    AcAction,
    AcActionBlock,
    AcScrollView,
  },
  data() {
    return {
      modalShow: false,
      modalMainShowDelay: false,
      actualZIndex: 0,
      globalMinWidth: DEFAULT_SIZE.small.width,
      globalMinHeight: DEFAULT_SIZE.small.height,
      winWidth: window.innerWidth,
      winHeight: window.innerHeight,
      headerHeight: 0,
      footerHeight: 0,
      scrollInnerWidth: 0,
      scrollInnerHeight: 0,
      isResetModalPosition: false,
      isDrag: false,
    };
  },
  props: {
    value: Boolean,
    width: Number,
    height: Number,
    maxWidth: Number,
    maxHeight: Number,
    zIndex: Number,
    autoSize: Boolean,
    drag: {
      type: Boolean,
      default: true,
    },
    buttonLoading: Boolean,
    okButtonText: {
      type: String,
      default: '确认',
    },
    cancelButtonText: {
      type: String,
      default: '取消',
    },
    okButtonType: {
      type: String,
      default: 'primary',
    },
    cancelButtonType: {
      type: String,
      default: 'primary',
    },
    okOnClose: {
      type: Boolean,
      default: true,
    },
    size: {
      type: String,
      default: 'primary',
      validator(value) {
        return globalAssistService.validate.oneOf(value, ['small', 'primary', 'large']);
      },
    },
    bodyPadding: {
      type: String,
      default: 'primary',
      validator(value) {
        return globalAssistService.validate.oneOf(value, ['small', 'primary', 'large']);
      },
    },
    lockScroll: {
      type: Boolean,
      default: true,
    },
    closable: {
      type: Boolean,
      default: true,
    },
    cancelable: {
      type: Boolean,
      default: true,
    },
    okable: {
      type: Boolean,
      default: true,
    },
    closeCallback: {
      type: Function,
      default() {
        return true;
      },
    },
    maskClosable: {
      type: Boolean,
      default: false,
    },
    transitionName: {
      type: String,
      default: 'fade-in',
    },
    appendToBody: {
      type: Boolean,
      default: true,
    },
    isRemove: {
      type: Boolean,
      default: false,
    },
    isFooter: {
      type: Boolean,
      default: true,
    },
    isHeader: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    globalMaxWidth() {
      return this.winWidth - DEFAULT_WINDOW_PADDING;
    },
    globalMaxHeight() {
      return this.winHeight - DEFAULT_WINDOW_PADDING;
    },
    normalizedWidth() {
      return this.normalizeNumber(this.width, DEFAULT_SIZE[this.size].width);
    },
    normalizedHeight() {
      return this.normalizeNumber(this.height, DEFAULT_SIZE[this.size].height);
    },
    normalizedMaxWidth() {
      return this.normalizeNumber(this.maxWidth, this.globalMaxWidth);
    },
    normalizedMaxHeight() {
      return this.normalizeNumber(this.maxHeight, this.globalMaxHeight);
    },
    modalWidth() {
      return this.autoSize ? 'auto' : `${this.initWidth}px`;
    },
    modalHeight() {
      return this.autoSize ? 'auto' : `${this.initHeight}px`;
    },
    modalMaxWidth() {
      return this.autoSize ? `${this.initMaxWidth}px` : undefined;
    },
    modalMaxHeight() {
      return this.autoSize ? `${this.initMaxHeight}px` : undefined;
    },
    initWidth() {
      return Math.max(this.globalMinWidth, Math.min(this.globalMaxWidth, this.normalizedWidth));
    },
    initHeight() {
      return Math.max(this.globalMinHeight, Math.min(this.globalMaxHeight, this.normalizedHeight));
    },
    initMaxWidth() {
      return Math.max(this.globalMinWidth, Math.min(this.globalMaxWidth, this.normalizedMaxWidth));
    },
    initMaxHeight() {
      return Math.max(this.globalMinHeight, Math.min(this.globalMaxHeight, this.normalizedMaxHeight));
    },
    modalPositionStyle() {
      return this.isResetModalPosition ? { left: 'auto', top: 'auto' } : {};
    },
  },
  watch: {
    value(val) {
      this.modalShow = val;
    },
    modalShow(val) {
      /**
       * TODO: Due to the rendering problem of position and flex attributes under Firefox,
       * the pop-up layer body is delayed.
       */
      setTimeout(() => {
        this.modalMainShowDelay = val;
      });

      this.$emit('input', val);

      if (val) {
        this.showPopup();
        this.calculateActualZIndex();

        this.$nextTick(() => {
          this.headerHeight = this.$refs.header ? this.$refs.header.scrollHeight : 0;
          this.footerHeight = this.$refs.footer ? this.$refs.footer.scrollHeight : 0;
        });
        this.isResetModalPosition = false;
      } else {
        this.hidePopup();
        setTimeout(() => {
          this.isResetModalPosition = this.drag;
        }, 300);
      }
    },
  },
  methods: {
    destroyElement() {
      if (this.isRemove) {
        this.$destroy(true);
        this.$el.parentNode.removeChild(this.$el);
      } else {
        this.$emit('on-after-leave');
      }
    },
    onMask() {
      if (this.maskClosable) {
        this.modalShow = !this.closeCallback();
      }
      if (!this.modalShow) {
        this.$emit('on-mask');
      }
    },
    onClose() {
      this.modalShow = !this.closeCallback();
      if (!this.modalShow) {
        this.$emit('on-close');
      }
    },
    onCancel() {
      this.modalShow = !this.closeCallback();
      if (!this.modalShow) {
        this.$emit('on-cancel');
      }
    },
    onOk() {
      this.$emit('on-ok');
      if (this.okOnClose) {
        this.modalShow = !this.closeCallback();
      }
    },
    resetWindowSize() {
      this.winWidth = window.innerWidth;
      this.winHeight = window.innerHeight;
    },
    calculateActualZIndex() {
      if (typeof this.zIndex === 'undefined') {
        this.actualZIndex = utilService.popup.nextZIndex();
      } else {
        this.actualZIndex = this.zIndex;
      }
    },
    normalizeNumber(val, defaultVal) {
      return (typeof val !== 'undefined' && val > 0) ? val : defaultVal;
    },
    bodyOverflowHidden() {
      const bodyOverflow = window.getComputedStyle(document.body).overflow;
      document.body.setAttribute('data-overflow', bodyOverflow);
      document.body.style.overflow = 'hidden';
    },
    bodyOverflowRecovery() {
      const bodyOverflow = document.body.getAttribute('data-overflow');
      document.body.removeAttribute('data-overflow');
      document.body.style.overflow = bodyOverflow;
    },
    showPopup() {
      utilService.popup.add();

      if (utilService.popup.size() === 1 && this.lockScroll) {
        this.bodyOverflowHidden();
      }
    },
    hidePopup() {
      utilService.popup.remove();

      if (utilService.popup.size() === 0 && this.lockScroll) {
        this.bodyOverflowRecovery();
      }
    },
    setScrollInnerSize() {
      if (!this.autoSize && !this.$refs.body) return;

      this.scrollInnerHeight = this.$refs.body.getInnerSize().sh;
      this.scrollInnerWidth = this.$refs.body.getInnerSize().sw;
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.modalShow = this.value;
      if (this.appendToBody) {
        document.body.appendChild(this.$el);
      }

      setTimeout(() => {
        this.isDrag = this.drag;
      }, 1000);

      window.addEventListener('resize', this.resetWindowSize);
    });
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resetWindowSize);
    this.$el.parentNode.removeChild(this.$el);
  },
};
</script>
