<template>
  <div :class="['ac-panel-header', {'ac-panel-header--section-like': asSectionHeader },{'ac-panel-header--section-like-border': asSectionHeaderBorder}]">
    <div :class="['ac-panel-header__inner', {'ac-panel-header__inner--clickable': collapsible}, 'ac-layout', 'ac-layout--center-items']">
      <div :class="['ac-panel-header__trigger']" v-if="collapsible">
        <ac-icon :type="collapsed ? 'right' : 'down'" size="small" as-block></ac-icon>
      </div>
      <div :class="['ac-panel-header__title', 'ac-typo-title']">
        <slot></slot>
      </div>
      <div :class="['ac-layout__flex']"></div>
      <div :class="['ac-panel-header__actions', 'ac-layout', 'ac-layout--center-items']"
        v-if="hasActions"
      >
        <slot name="actions"></slot>
      </div>
      <div :class="['ac-panel-header__builtin-actions']" v-if="hasBuiltinActions"></div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'AcPanelHeader',
  props: {
    collapsible: {
      type: Boolean,
      required: true,
    },
    collapsed: {
      type: Boolean,
      default: false,
    },
    withFullScreenAction: {
      type: Boolean,
      default: false,
    },
    asSectionHeader: {
      type: Boolean,
      default: false,
    },
    asSectionHeaderBorder: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    hasActions() {
      return (this.$slots.actions && this.$slots.actions.length > 0);
    },
    hasBuiltinActions() {
      return this.withFullScreenAction;
    },
  },
};
</script>
