/* eslint-disable import/prefer-default-export */
import { globalAssistService } from '$src/utils';

const DragDir = {
	inserted(el) {
		const oDiv = el;
		oDiv.onmousedown = (ev) => {
			if (!globalAssistService.dom.hasClass(ev.target, 'ac-moadl__header--filling')) return;
			const disX = ev.clientX - oDiv.offsetLeft;
			const disY = ev.clientY - oDiv.offsetTop;
			const width = oDiv.offsetWidth;
			const height = oDiv.offsetHeight;
			document.onmousemove = (_ev) => {
				const [
					clientWidth,
					clientHeight,
				] = [document.body.clientWidth, document.body.clientHeight];
				let l = Math.max(_ev.clientX - disX, 4);
				let t = Math.max(_ev.clientY - disY, 4);
				if (l + width + 4 > clientWidth) { l = clientWidth - 4 - width; }
				if (t + height + 4 > clientHeight) { t = clientHeight - 4 - height; }
				oDiv.style.left = `${l}px`;
				oDiv.style.top = `${t}px`;
			};
			document.onmouseup = () => {
				document.onmousemove = null;
				document.onmouseup = null;
			};
		};
	},
};

export {
	DragDir,
};
