<template>
  <transition name="fade">
    <div
      :class="[
        'ac-loading-animation-default-wrap', 
        'ac-layout--inline', 
        'ac-layout--center-items',
        wrapClasses,
      ]">
      <div class="ac-loading-animation-default">
        <div class="ac-loading-animation-default__dot"><div></div></div>
        <div class="ac-loading-animation-default__dot"></div>
        <div class="ac-loading-animation-default__dot"></div>
        <div class="ac-loading-animation-default__dot"></div>
      </div>
      <div class="ac-loading-animation-default__tip" v-html="tip" v-if="tip"></div>
    </div>

  </transition>
</template>

<script>

export default {
  name: 'AcLoadingAnimationDefault',
  props: {
    tip: String,
    size: String,
  },
  computed: {
    wrapClasses() {
      return `ac-loading-animation-default-wrap--${this.size}`;
    },
  },
};
</script>
