<script>
import AcTabNav from './ac-tab-nav';
import AcTabPage from './ac-tab-page';

/**
 * Events:
 * @event on-select `(tabId: string)`
 * @event on-close `(tabId: string)`
 */
export default {
  name: 'AcTabGroup',
  props: {
    value: {
      type: String,
      required: true,
    },
    tabsProps: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  model: {
    prop: 'value',
    event: 'on-select',
  },
  provide() {
    return {
      claimTabSelected: this.claimTabSelected,
      claimTabClose: this.claimTabClose,
    };
  },
  data() {
    return {
      /**
       * Collected from children tab pages. Because the beforeUpdate() hook bug of vue,
       * for now we have to fallback to use `props` as the definition source of tabs.
      ```js
      {
        tabId: string,
        label?: string,
        icon?: string | { type: string, name: string },
        disabled?: boolean,
        closable?: boolean,
      }
      ```
       */
      // tabsProps: [],
    };
  },
  methods: {
    collectTabsProps() {
      if (!this.$slots.default || !Array.isArray(this.$slots.default)) { return; }

      this.tabsProps.splice(0);
      this.$slots.default.forEach((node) => {
        if (!node.componentOptions || node.componentOptions.tag !== 'ac-tab-page' || !node.componentOptions.propsData) { return; }

        const props = node.componentOptions.propsData;

        this.tabsProps.push({
          tabId: props.tabId,
          label: props.label,
          icon: props.icon,
          disabled: !(props.disabled === undefined || props.disabled === false),
          closable: !(props.closable === undefined || props.closable === false),
        });
      });
    },
    hasExtra() {
      return this.$slots.extra && this.$slots.extra.length > 0;
    },
    pickPageNode() {
      if (!this.$slots.default || !Array.isArray(this.$slots.default)) { return null; }
      return this.$slots.default.find((node) => {
        if (!node.componentOptions || node.componentOptions.tag !== 'ac-tab-page') { return false; }
        if (!node.componentOptions.propsData || node.componentOptions.propsData.tabId !== this.value) { return false; }
        return true;
      });
    },
    claimTabSelected(tabId) {
      this.$emit('on-select', tabId);
    },
    claimTabClose(tabId) {
      this.$emit('on-close', tabId);
    },
  },
  beforeMount() {
    // this.collectTabsProps();
  },
  beforeUpdate() {
    // this.collectTabsProps();
  },
  render() {
    let barExtraNode = null;

    if (this.hasExtra()) {
      barExtraNode = (
        <div class={['ac-tab-group__bar__extra', 'ac-layout__flex-none',
                    'ac-layout', 'ac-layout--center-items']}
        >
          {this.$slots.extra}
        </div>
      );
    }

    const rootNode = (
      <div class={['ac-tab-group', 'ac-layout', 'ac-layout--column']}>
        <div class={['ac-tab-group__bar', 'ac-layout', 'ac-layout--between-justify']}>
          <ac-tab-nav class={['ac-layout__flex']} tabs={this.tabsProps} activeTabId={this.value}></ac-tab-nav>
          {barExtraNode}
        </div>
        <div class={['ac-tab-group__pages']}>
          {this.pickPageNode()}
        </div>
      </div>
    );
    return rootNode;
  },
  components: {
    AcTabNav,
    AcTabPage,
  },
};
</script>

