<template>
  <div ref="headerCont" :class="['ac-table-header']">
    <table :class="['ac-table-header__table']" :style="[tableStyles]">
      <colgroup>
        <col
          v-for="col in effectiveColumnsConfig"
          :key="col.key"
          :style="getColStyles(col)"
          :class="getColClasses(col)"
        />
      </colgroup>
      <thead>
        <tr
          v-for="(layer, index) in layeredColumnsConfig"
          :key="index"
          :class="['ac-table-header__table__row']"
        >
          <th
            v-for="colDesc in layer"
            :key="colDesc.col.key"
            :rowspan="colDesc.rowspan"
            :colspan="colDesc.colspan"
            :class="['ac-table-header__table__row__cell',
              'ac-typo-small-title',
              {'ac-table-header__table__row__cell--bottommost': isBottommostCell(colDesc)},
              {'ac-table-header__table__row__cell--leftmost': isLeftmostCell(colDesc)},
              {'ac-table-header__table__row__cell--rightmost': isRightmostCell(colDesc)},
              {'ac-table-header__table__row__cell--border': border},
            ]"
          >
            <div :class="['ac-table-header__table__row__cell__wrapper', 'ac-layout']">
              <div :class="['ac-table-header__table__row__cell__wrapper__cont',
                  'ac-layout__flex',
                  'ac-layout', 'ac-layout--center-items',
                  {'ac-layout--center-justify': isCenteredCell(colDesc)},
                  {[`ac-table-header__table__row__cell__wrapper__cont--${size}`]: size !== 'medium'},
                ]"
              >
                <template v-if="colDesc.col.fixed === true && !forFixedPortion">
                  <!-- nothing to show here -->
                </template>
                <template v-else-if="colDesc.col.type === 'index'">
                  <span :class="['ac-layout__flex-none']">{{ '#' }}</span>
                </template>
                <template v-else-if="colDesc.col.type === 'selection'">
                  <span :class="['ac-layout__flex-none', 'ac-layout', 'ac-layout--center-items']">
                    <Checkbox
                      :value="selectedStatus === 'all'"
                      :indeterminate="selectedStatus === 'partial'"
                      @on-change="onChangeSelectStatus"
                    ></Checkbox>
                    <Dropdown v-if="!!colDesc.col.typeOptions" placement="bottom-start">
                      <span :style="{cursor: 'pointer'}"><ac-icon type="down" size="xsmall" as-block></ac-icon></span>
                      <DropdownMenu slot="list">
                        <DropdownItem
                          v-for="item in colDesc.col.typeOptions.selectedOperationOptions"
                          :key="item.text"
                          @click.native="item.click"
                        >{{ item.text }}</DropdownItem>
                      </DropdownMenu>
                    </Dropdown>
                  </span>
                </template>
                <template v-else-if="colDesc.col.type === 'expand'">
                  <!-- nothing to show here -->
                </template>
                <template v-else>
                  <span :class="['ac-table-header__table__row__cell__wrapper__cont__text']"
                  >{{ colDesc.col.title }}</span>
                  <div v-if="isSortableCol(colDesc.col)" :class="['ac-layout__flex-none']">
                    <div
                      :class="['ac-table-header__sorter-wrapper',
                        {'ac-table-header__sorter-wrapper--active': isSortingActive(colDesc.col.key, 'asc')},
                      ]"
                      @click="onRequestSort(colDesc.col.key, 'asc')"
                    >
                      <ac-icon type="arrow-up-b" size="small"></ac-icon>
                    </div>
                    <div
                      :class="['ac-table-header__sorter-wrapper',
                        {'ac-table-header__sorter-wrapper--active': isSortingActive(colDesc.col.key, 'desc')},
                      ]"
                      @click="onRequestSort(colDesc.col.key, 'desc')"
                    >
                      <ac-icon type="arrow-down-b" size="small"></ac-icon>
                    </div>
                  </div>
                  <div :class="['ac-table-header__table__row__cell__wrapper__cont__fill']"></div>
                </template>
              </div>
              <div v-if="shouldShowColAdjustHandle(colDesc)"
                :class="['ac-table-header__table__row__cell__wrapper__adjust-handle', 'ac-layout__flex-none']"
              >
                <div :class="['ac-table-header__table__row__cell__wrapper__adjust-handle__handle',
                    { 'ac-table-header__table__row__cell__wrapper__adjust-handle__handle--with-border': border },
                  ]"
                  @mouseenter="onEnterAdjustHandle($event, colDesc.col)"
                  @mouseleave="onLeaveAdjustHandle"
                ></div>
              </div>
            </div>
          </th>
        </tr>
      </thead>
    </table>
  </div>
</template>

<script>
import { globalAssistService } from '$src/utils';
import { AcIcon } from '../ac-icon';
import utilService from './util.service';

export default {
  name: 'AcTableHeader',
  props: {
    columnsConfig: {
      type: Array,
      required: true,
    },
    layeredColumnsConfig: {
      type: Array,
      required: true,
    },
    effectiveColumnsConfig: {
      type: Array,
      required: true,
    },
    columnAdjustable: {
      type: Boolean,
      required: true,
    },
    sortedColumns: {
      type: Array,
      required: true,
    },
    size: {
      type: String,
      required: true,
    },
    border: {
      type: Boolean,
      required: true,
    },
    /** {left, top, width, height} */
    bodyMetrics: {
      type: Object,
      required: true,
    },
    selectedStatus: {
      type: String,
    },
    forFixedPortion: {
      type: Boolean,
      default: false,
    },
  },
  inject: ['claimSelectAllEntries', 'claimSortRequest', 'claimColResizerTrigger'],
  computed: {
    tableStyles() {
      const styles = {};
      if (this.forFixedPortion) {
        const width = utilService.calcTableWidth(this.effectiveColumnsConfig);
        styles.width = `${width}px`;
      } else if (this.bodyMetrics.width !== null && this.bodyMetrics.width !== undefined) {
        const paddingRight = (this.bodyMetrics.height > this.bodyMetrics.clientHeight) ? globalAssistService.dom.getScrollBarSize() : 0;
        styles.width = `${this.bodyMetrics.width + paddingRight}px`;
        if (paddingRight > 0) {
          styles.paddingRight = `${paddingRight}px`;
        }
      }
      return styles;
    },
    leftmostColumns() {
      const lcs = [];
      let firstCol = this.columnsConfig[0];

      while (firstCol) {
        lcs.push(firstCol);
        firstCol = firstCol.children ? firstCol.children[0] : null;
      }

      return lcs;
    },
    rightmostColumns() {
      const rcs = [];
      let lastCol = this.columnsConfig[this.columnsConfig.length - 1];

      while (lastCol) {
        rcs.push(lastCol);
        lastCol = lastCol.children ? lastCol.children[lastCol.children.length - 1] : null;
      }

      return rcs;
    },
  },
  methods: {
    getColStyles: utilService.getColStyles,
    getColClasses: utilService.getColClasses,
    isCenteredCell(colDesc) {
      return colDesc && colDesc.colspan > 1;
    },
    isBottommostCell(colDesc) {
      return this.effectiveColumnsConfig.indexOf(colDesc.col) >= 0;
    },
    isLeftmostCell(colDesc) {
      return this.leftmostColumns.indexOf(colDesc.col) >= 0;
    },
    isRightmostCell(colDesc) {
      return this.rightmostColumns.indexOf(colDesc.col) >= 0;
    },
    isSortableCol(col) {
      return (typeof col.sorter === 'function' || typeof col.sorter === 'boolean');
    },
    isSortingActive(colKey, type) {
      const colSortDesc = this.sortedColumns.find(sortDesc => (sortDesc.colKey === colKey));
      if (!colSortDesc) { return false; }
      return colSortDesc.ascending ? (type === 'asc') : (type === 'desc');
    },
    shouldShowColAdjustHandle(colDesc) {
      return this.columnAdjustable
        && !this.forFixedPortion
        && !colDesc.col.type // we don't want user to adjust those pre-defined columns
        && !this.isRightmostCell(colDesc);
    },
    onChangeSelectStatus(selected) {
      this.claimSelectAllEntries(selected);
    },
    onRequestSort(colKey, type) {
      const isActive = this.isSortingActive(colKey, type);
      this.claimSortRequest(colKey, isActive ? 'cancel' : type);
    },
    /** @public */
    updatePosition({ left }) {
      this.$refs.headerCont.scrollLeft = left;
    },
    onEnterAdjustHandle(event, col) {
      this.claimColResizerTrigger({
        rect: event.currentTarget.getBoundingClientRect(),
        col,
        colEl: event.currentTarget.parentElement.parentElement.parentElement, // be careful, we are targeting the <th>
      });
    },
    onLeaveAdjustHandle(event) {
      this.claimColResizerTrigger({
        rect: null,
        relatedTarget: event.relatedTarget,
      });
    },
  },
  components: {
    AcIcon,
  },
};
</script>
