<template>
    <div ref="acEcharts" :class="['ac-echarts', 'ac-layout', { 'ac-echarts--shadow': shadow  }]" :style="[styles]">
        <div ref="acEchartsWrap" class="ac-echarts__wrap ac-layout__flex" v-show="!loading && hasData" key="chartWrap"></div>

        <div class="ac-echarts__text ac-layout__flex ac-layout ac-layout--center-items ac-layout--center-justify" v-show="!loading && !hasData" key="chartText">
            <span>{{ noDataText }}</span>
        </div>

        <ac-loading class="ac-layout__flex" v-show="loading" key="chartLoading"></ac-loading>
    </div>
</template>

<script>
import * as echarts from "echarts";
import { globalAssistService } from "$src/utils";

export default {
  name: "AcEcharts",

  props: {
    data: Object,
    width: {
      type: [Number, String],
      default: "auto"
    },
    height: {
      type: [Number, String],
      default: 420
    },
    noDataText: {
      type: [String, Number],
      default: "暂无数据"
    },
    loading: Boolean,
    shadow: Boolean
  },

  data() {
    return {
      chartInstance: null
    };
  },

  computed: {
    styles() {
      return {
        height: globalAssistService.validate.hasCharacter(
          this.height + "",
          "px,%,auto"
        )
          ? this.height
          : `${this.height}px`,
        width: globalAssistService.validate.hasCharacter(
          this.width + "",
          "px,%,auto"
        )
          ? this.width
          : `${this.width}px`
      };
    },

    hasData() {
      return (
        this.data &&
        globalAssistService.validate.typeOf(this.data) === "object" &&
        this.validateSeriesData(this.data.series)
      );
    }
  },

  watch: {
    data: {
      handler() {
        this.$nextTick(() => {
          this.printEcharts();
        });
      },
      deep: true
    }
  },

  methods: {
    createEchartsInstance() {
      if (this.chartInstance) return;
      this.chartInstance = echarts.init(this.$refs.acEchartsWrap);
    },

    printEcharts() {
      if (!this.loading && this.hasData) {
        this.chartInstance.resize();
        this.chartInstance.setOption(this.data);
      }
    },

    validateSeriesData(payload = []) {
      let isPass = false;

      payload.every(item => {
        if (
          globalAssistService.validate.typeOf(item.data) === "array" &&
          item.data.length > 0
        ) {
          isPass = true;
          return false;
        }
        return true;
      });

      return isPass;
    },
  },

  mounted() {
    this.$nextTick(() => {
      this.createEchartsInstance();

      this.printEcharts();
    });
  }
};
</script>
