<template>
  <div :class="classes" :style="styles">
    <slot>
      <span :class="classesTitle">{{ title }}</span>
      <a :class="['ac-sprite__block__explain']" v-if="explainCallback" @click="explainHandel">{{ explain }}</a>
      <span :class="['ac-sprite__block__explain']" v-else>{{ explain }}</span>
    </slot>
  </div>
</template>

<script>
import { globalAssistService } from '$src/utils';

export default {
  name: 'AcSpriteBlock',

  props: {
    title: [String, Number],
    explain: [String, Number],
    explainCallback: Function,
    imgUrl: String,
    typeTitle: {
      type: String,
      default: 'primary',
      validator(value) {
        return globalAssistService.validate.oneOf(value, ['primary', 'success', 'warning', 'error']);
      },
    },
    type: {
      type: String,
      default: 'primary',
      validator(value) {
        return globalAssistService.validate.oneOf(value, ['info', 'success', 'warning', 'error', 'primary']);
      },
    },
  },

  computed: {
    styles() {
      if (this.imgUrl) {
        return {
          background: `url(${this.imgUrl}) no-repeat`,
          'background-size': `100% 100%`,
        };
      }
      return null;
    },

    classes() {
      return [
        'ac-layout__flex',
        'ac-sprite__block',
        'ac-layout',
        'ac-layout--center-items',
        'ac-layout--center-justify',
        'ac-layout--column',
        {
          [`ac-sprite__block--${this.type}`]: !!!this.imgUrl,
        },
      ];
    },

    classesTitle() {
      return [
        'ac-sprite__block__title',
        `ac-sprite__block__title--${this.typeTitle}`,
      ];
    },
  },

  methods: {
    explainHandel() {
      this.explainCallback();
    },
  },
};
</script>
