<template>
    <div
      :class="[
        'ac-table-toolbar',
        inline ? 'ac-layout--inline' : 'ac-layout',
      ]"
      :style="styles">
      <slot></slot>
    </div>
</template>

<script>
import { globalAssistService } from '$src/utils';

export default {
  name: 'AcTableToolbar',
  props: {
    height: {
      type: [Number, String],
      default: 45,
    },
    width: {
      type: [Number, String],
      default: 'auto',
    },
    inline: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    styles() {
      const style = {};

      style.width = globalAssistService.validate.hasCharacter(this.width.toString(), '%,px,auto')
        ? this.width
        : `${this.width}px`;

      style.height = globalAssistService.validate.hasCharacter(this.height.toString(), '%,px,auto')
        ? this.height
        : `${this.height}px`;

      return style;
    },
  },
};
</script>
