<template>
  <ac-scroll-view
    :class="['ac-table-body', {'ac-table-body--no-scroll': forFixedPortion === 'left'}]"
    ref="bodyCont"
    @scroll="onBodyScroll"
    @content-dom-updated="onContentDomUpdated"
  >
    <table :class="['ac-table-body__table']" :style="tableStyles">
      <colgroup>
        <col
          v-for="col in effectiveColumnsConfig"
          :key="col.key"
          :style="getColStyles(col)"
          :class="getColClasses(col)"
        />
      </colgroup>
      <tbody>
        <template v-for="(entry, index) in data">
          <ac-table-row
            :key="entry._key"
            :entry="entry"
            :index="index + rowStartIndex"
            v-bind="rowProps"
            :selected="isEntrySelected(entry)"
            :expanded="isEntryExpanded(entry)"
          ></ac-table-row>
          <ac-table-row-expanded
            v-if="isEntryExpanded(entry)"
            :key="`${entry._key}_expanded`"
            :entry="entry"
            :index="index + rowStartIndex"
            v-bind="rowProps"
          ></ac-table-row-expanded>
        </template>
        <template v-if="data.length <= 0">
          <ac-table-row
            :entry="null"
            :index="0"
            v-bind="rowProps"
          ></ac-table-row>
        </template>
      </tbody>
    </table>
  </ac-scroll-view>
</template>

<script>
import _ from 'lodash';
import { AcScrollView } from '../ac-scroll-view';
import AcTableRow from './ac-table-row';
import AcTableRowExpanded from './ac-table-row-expanded';
import utilService from './util.service';

export default {
  name: 'AcTableBody',
  props: {
    columnsConfig: {
      type: Array,
      required: true,
    },
    effectiveColumnsConfig: {
      type: Array,
      required: true,
    },
    data: {
      type: Array,
      required: true,
    },
    accordion: {
      type: Boolean,
      required: true,
    },
    sortedColumns: {
      /** [{colKey: string, ascending: boolean}] */
      type: Array,
      required: true,
    },
    size: {
      type: String,
      required: true,
    },
    stripe: {
      type: Boolean,
      required: true,
    },
    border: {
      type: Boolean,
      required: true,
    },
    nestLevel: {
      type: Number,
      required: true,
    },
    indentSize: {
      type: Number,
      required: true,
    },
    selectedArr: {
      type: Array,
      default: [],
    },
    expandedArr: {
      type: Array,
      default: [],
    },
    pagination: {
      type: Object,
    },
    effectivePaginationConfig: {
      type: Object,
    },
    bodyMetrics: {
      type: Object,
      required: true,
    },
    forFixedPortion: {
      type: [Boolean, String],
      default: false,
    },
  },
  inject: ['claimBodyMetrics'],
  computed: {
    rowStartIndex() {
      if (this.pagination) {
        return (Math.max(this.pagination.pageNum - 1, 0) * this.pagination.pageSize);
      }
      return 0;
    },
    rowProps() {
      const {
        columnsConfig, size, stripe, border, nestLevel, indentSize, accordion,
      } = this.$props;
      return {
        columnsConfig,
        effectiveColumnsConfig: this.effectiveColumnsConfig,
        effectivePaginationConfig: this.effectivePaginationConfig,
        accordion,
        size,
        stripe,
        border,
        nestLevel,
        indentSize,
        forTreeTable: this.isForTreeTable,
        forFixedPortion: !!this.forFixedPortion,
      };
    },
    tableStyles() {
      return {
        minWidth: `${utilService.calcTableWidth(this.effectiveColumnsConfig)}px`,
      };
    },
    isForTreeTable() {
      return this.nestLevel > 0 || !this.data.every(entry => (!entry._children));
    },
  },
  methods: {
    /** @public */
    reinit() {
      if (this.$refs.bodyCont) {
        this.$refs.bodyCont.reinit();
      }
      this.reportBodyContMetrics();
    },
    getColStyles: utilService.getColStyles,
    getColClasses: utilService.getColClasses,
    isEntrySelected(entry) {
      return this.selectedArr.indexOf(entry._key) >= 0;
    },
    isEntryExpanded(entry) {
      return this.expandedArr.indexOf(entry._key) >= 0;
    },
    reportBodyMetrics(contComp) {
      const toReport = {};
      toReport.top = contComp.scrollTop;
      if (!this.forFixedPortion) {
        Object.assign(toReport, {
          left: contComp.scrollLeft,
          width: contComp.scrollWidth,
          clientWidth: contComp.clientWidth,
          height: contComp.scrollHeight,
          clientHeight: contComp.clientHeight,
        });
      }
      this.claimBodyMetrics(toReport);
    },
    reportBodyContMetrics() {
      if (!this.$refs.bodyCont) { return; }
      this.$refs.bodyCont.syncSizes();
      this.reportBodyMetrics(this.$refs.bodyCont);
    },
    delayReportBodyMetrics: null,
    shortDelayReportBodyMetrics: null,
    onBodyScroll() {
      this.reportBodyMetrics(this.$refs.bodyCont);
    },
    onResize() {
      if (this.forFixedPortion) { return; }
      if (!this.$refs.bodyCont) { return; }
      this.reportBodyMetrics({
        left: null,
        top: null,
        width: null,
        clientWidth: null,
        height: null,
        clientHeight: null,
      });
      this.delayReportBodyMetrics();
    },
    onContentDomUpdated() {
      this.reportBodyContMetrics();
      this.$emit('dom-updated');
    },
    updatePosition({ top }) {
      this.$refs.bodyCont.updatePosition(top, true);
    },
  },
  beforeCreate() {
    /** set per-instance debounce method */
    this.$options.methods.delayReportBodyMetrics = _.throttle(() => {
      this.reportBodyContMetrics();
    }, 600);
    this.$options.methods.shortDelayReportBodyMetrics = _.throttle(() => {
      this.reportBodyContMetrics();
    }, 100);
  },
  mounted() {
    this.$nextTick(() => {
      this.reportBodyContMetrics();
    });

    window.addEventListener('resize', this.onResize);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.onResize);
  },
  components: {
    AcScrollView,
    AcTableRow,
    AcTableRowExpanded,
  },
};
</script>
