<template>
  <ac-modal
    class="ac-confirm-wrap"
    v-model="modalShow"
    size="small"
    :appendToBody="false"
    @on-close="onClose"
    @on-mask="onMask"
    @on-ok="onOk"
    @on-cancel="onConcle"
    @on-after-leave="destroyElement"
  >
    <template slot="title">{{ title }}</template>
    {{ content }}
  </ac-modal>
</template>

<script>
import { AcModal } from '../ac-modal';

export default {
  name: 'AcConfirm',
  components: {
    AcModal,
  },
  data() {
    return {
      modalShow: false,
      title: '',
      content: '',
    };
  },
  methods: {
    destroyElement() {
      this.$destroy(true);
    },
    onClose() {
      this.$emit('on-close-confirm');
    },
    onMask() {
      this.$emit('on-mask-confirm');
    },
    onOk() {
      this.$emit('on-ok-confirm');
    },
    onConcle() {
      this.$emit('on-cancel-confirm');
    },
  },
};
</script>
