<template>
  <div :class="['ac-panel-group', 'ac-panel-group--shadow']">
    <slot><!-- can only use AcPanel here --></slot>
  </div>
</template>

<script>
/**
 * Container for group of panels
 */
export default {
  name: 'AcPanelGroup',
  props: {
    accordion: {
      type: Boolean,
      default: false,
    },
    initialExpandedPanel: {
      type: String,
      default: null,
    },
    borderless: {
      type: Boolean,
      default: false,
    },
  },
  provide() {
    return {
      acPanelGroupParam: this.acPanelGroupParam,
      claimLastExpandedPanel: this.claimLastExpandedPanel,
    };
  },
  data() {
    return {
      acPanelGroupParam: {
        accordion: this.accordion,
        borderless: this.borderless,
        lastExpandedPanel: this.initialExpandedPanel,
      },
    };
  },
  methods: {
    claimLastExpandedPanel(panelId) {
      this.acPanelGroupParam.lastExpandedPanel = panelId;
    },
  },
};
</script>
