/* eslint-disable no-param-reassign */
import Vue from 'vue';
import AcConfirm from './ac-confirm';

const Confirm = Vue.extend(AcConfirm);

const noop = () => { };

const DEFAULT_OPTIONS = {
	title: '确认',
	content: '确认当前操作吗？',
	onCancel: noop,
	onConfirm: noop,
	target: document.body,
};

export default function (options) {
	options = Object.assign({}, DEFAULT_OPTIONS, options);

	if (typeof options.target === 'string') {
		options.target = document.querySelector(options.target) || DEFAULT_OPTIONS.target;
	}
	const vm = new Confirm().$mount();
	options.target.appendChild(vm.$el);
	Object.assign(vm, {
		title: options.title,
		content: options.content,
		modalShow: true,
	});

	function close() {
		if (typeof options.onConfirm !== 'function') {
			return;
		}

		const result = options.onConfirm();

		if (result === false) {
			return;
		}

		if (result && typeof result.then === 'function') {
			result.then(() => {
				vm.modalShow = false;
			});
			return;
		}

		vm.modalShow = false;
	}
	function dismiss() {
		if (typeof options.onCancel !== 'function') {
			return;
		}

		const result = options.onCancel();

		if (result === false) {
			return;
		}

		if (result && typeof result.then === 'function') {
			result.then(() => {
				vm.modalShow = false;
			});
			return;
		}

		vm.modalShow = false;
	}

	vm.$on('on-close-confirm', dismiss);
	vm.$on('on-mask-confirm', dismiss);
	vm.$on('on-ok-confirm', close);
	vm.$on('on-cancel-confirm', dismiss);
}
