<script>
import { AcIcon } from '../ac-icon';

export default {
  name: 'AcTableRow',
  props: {
    columnsConfig: {
      type: Array,
      required: true,
    },
    effectiveColumnsConfig: {
      type: Array,
      required: true,
    },
    entry: {
      type: Object,
      required: false,
    },
    index: {
      type: Number,
      required: true,
    },
    size: {
      type: String,
      required: true,
    },
    stripe: {
      type: Boolean,
      required: true,
    },
    border: {
      type: Boolean,
      required: true,
    },
    nestLevel: {
      type: Number,
      required: true,
    },
    indentSize: {
      type: Number,
      required: true,
    },
    selected: {
      type: Boolean,
      default: false,
    },
    expanded: {
      type: Boolean,
      default: false,
    },
    forTreeTable: {
      type: Boolean,
      default: false,
    },
    forFixedPortion: {
      type: Boolean,
      required: true,
    },
  },
  inject: ['claimEntrySelected', 'claimEntryExpanded'],
  computed: {
    treeCellWrapperStyles() {
      const styles = {};
      if (this.nestLevel > 0) {
        styles.paddingLeft = `${this.nestLevel * this.indentSize}px`;
      }
      return styles;
    },
    isWithFixedColumns() {
      return this.effectiveColumnsConfig.length &&
        (this.effectiveColumnsConfig[0].fixed === true || this.effectiveColumnsConfig[this.effectiveColumnsConfig.length - 1].fixed === true);
    },
  },
  methods: {
    onChangeSelected(selected) {
      this.claimEntrySelected(this.entry, selected);
    },
    onClickExpandTrigger() {
      this.claimEntryExpanded(this.entry, !this.expanded);
    },
  },
  render() {
    const cellsNodes = [];
    const isTreeRow = this.entry ? Array.isArray(this.entry._children) : false;
    let treeCellFound = false;
    const that = this;
    const tdExtraClasses = [];

    function getTreeExpandTrigger(expanded) {
      return (
        <div class={['ac-table-row__cell__trigger', 'ac-table-row__cell__trigger--inline']}>
          <ac-icon
            type={expanded ? 'down' : 'right'}
            size="small"
            as-block
            nativeOnClick={that.onClickExpandTrigger}
          ></ac-icon>
        </div>
      );
    }

    if (this.border) {
      tdExtraClasses.push('ac-table-row__cell--border');
    }
    if (this.nestLevel > 0) {
      tdExtraClasses.push('ac-table-row__cell--nested');
    }
    if (this.size !== 'medium') {
      tdExtraClasses.push(`ac-table-row__cell--${this.size}`);
    }
    if (this.isWithFixedColumns) {
      tdExtraClasses.push('ac-table-row__cell--with-fixed-col');
    }

    if (this.entry) {
      this.effectiveColumnsConfig.forEach((colConfig) => {
        let value;
        let isTreeCell = false;

        if (colConfig.fixed === true && !this.forFixedPortion) {
          /** we don't want to render the content of this cell as it is in the fixed column but not in the fixed portion */
          value = null;
        } else if (colConfig.type) {
          if (colConfig.type === 'index') {
            value = String(this.index + 1);
          } else if (colConfig.type === 'selection') {
            value = [
              <checkbox value={this.selected} {...{ on: { 'on-change': this.onChangeSelected } }}></checkbox>,
            ];
            if (colConfig.typeOptions && colConfig.typeOptions.forEveryRow === true && this.selected) {
              value.push((
                <dropdown placement="bottom-start">
                  <span style={{ cursor: 'pointer' }}><ac-icon type="down" size="xsmall" as-block></ac-icon></span>
                  <dropdownMenu slot="list">
                  {
                    colConfig.typeOptions.selectedOperationOptions.map(item => (
                        <dropdownItem key={item.text} nativeOnClick={item.click}>{item.text}</dropdownItem>
                    ))
                  }
                  </dropdownMenu>
                </dropdown>
              ));
            }
          } else if (colConfig.type === 'expand') {
            value = (
              <div class="ac-table-row__cell__trigger">
                <ac-icon
                  type={this.expanded ? 'down' : 'right'}
                  size="small"
                  as-block
                  nativeOnClick={this.onClickExpandTrigger}
                ></ac-icon>
              </div>
            );
          }
        } else if (colConfig.render instanceof Function) {
          value = colConfig.render.call(this, this.$createElement, {
            entry: this.entry,
            index: this.index,
          });
          if (!treeCellFound) {
            isTreeCell = true;
          }
        } else {
          value = this.entry[colConfig.dataKey];
          if (!treeCellFound) {
            isTreeCell = true;
          }
        }
        if (value === undefined) {
          value = '';
        }

        if (isTreeCell && this.forTreeTable) {
          value = (
            <span class="ac-table-row__cell__tree-cell-wrapper" style={this.treeCellWrapperStyles}>
              {isTreeRow ? getTreeExpandTrigger(this.expanded) : undefined}
              {value}
            </span>
          );
          treeCellFound = true;
        }

        const extraClasses = [].concat(tdExtraClasses);
        if (colConfig.className) {
          extraClasses.push(colConfig.className);
        }

        const cellNode = (
          <td class={['ac-table-row__cell', ...extraClasses]}>
            {value}
          </td>
        );

        cellsNodes.push(cellNode);
      });
    } else {
      cellsNodes.push((
        <td class={['ac-table-row__cell', ...tdExtraClasses]} colspan={this.effectiveColumnsConfig.length}>暂无数据</td>
      ));
    }

    const extraClasses = [];
    if (this.stripe) {
      if (this.index % 2) {
        extraClasses.push('ac-table-row--contrast');
      }
    }

    const rootNode = (
      <tr class={['ac-table-row', ...extraClasses]}>
        {cellsNodes}
      </tr>
    );

    return rootNode;
  },
  components: {
    AcIcon,
  },
};
</script>
