<template>
  <div :class="classes">
    <slot></slot>
  </div>
</template>

<script>
import { globalAssistService } from '$src/utils';

export default {
  name: 'AcSprite',
  props: {
    shadow: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: 'primary',
      validator(value) {
        return globalAssistService.validate.oneOf(value, ['primary', 'small', 'large']);
      },
    },
  },
  computed: {
    classes() {
      return [
        'ac-sprite',
        'ac-layout',
        {
          'ac-sprite--shadow': this.shadow,
          'ac-sprite--no-shadow': !this.shadow,
        },
      ];
    },
  },
};
</script>
