<template>
<div :class="['ac-tab-nav', 'ac-layout']">
  <div :class="['ac-tab-nav__mover', 'ac-tab-nav__mover--backward', 'ac-layout']"
    @click.stop.prevent="onClickMover('backward')"
    v-show="showMover"
  >
    <div :class="['ac-tab-nav__mover__inner', 'ac-layout', 'ac-layout--center-items']">
      <ac-icon type="left" size="small" as-block></ac-icon>
    </div>
  </div>
  <div :class="['ac-layout__flex']">
    <div :class="['ac-tab-nav__cont', 'ac-layout']" ref="cont">
      <div :class="['ac-tab-nav__cont__tabs-cont', 'ac-layout__flex-none']" :style="{left: `${tabsContLeft}px`}" ref="tabsCont">
        <ul :class="['ac-tab-nav__cont__tabs-cont__list', 'ac-layout']">
          <ac-tab v-for="tab in tabs"
            :key="tab.tabId"
            v-bind="normalizeTabProps(tab)"
            :active="tab.tabId === activeTabId"
            :class="['ac-layout__flex-none']"
          ></ac-tab>
        </ul>
        <div ref="tabsContSizeSensor" class="ac-tab-nav__cont__tabs-cont__size-sensor"></div>
      </div>
    </div>
    <div ref="contSizeSensor" class="ac-tab-nav__cont__tabs-cont__size-sensor"></div>
  </div>
  <div :class="['ac-tab-nav__mover', 'ac-tab-nav__mover--forward', 'ac-layout']"
    @click.stop.prevent="onClickMover('forward')"
    v-show="showMover"
  >
    <div :class="['ac-tab-nav__mover__inner', 'ac-layout', 'ac-layout--center-items']">
      <ac-icon type="right" size="small" as-block></ac-icon>
    </div>
  </div>
</div>
</template>

<script>
import { coreAssistService } from '$examples/core/utils';
import AcTab from './ac-tab';

export default {
  name: 'AcTabNav',
  props: {
    tabs: {
      type: Array,
      required: true,
    },
    activeTabId: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      showMover: false,
      tabsContLeft: 0,
    };
  },
  watch: {
    showMover(val) {
      if (!val) {
        this.tabsContLeft = 0;
      }
    },
  },
  methods: {
    normalizeTabProps(tabProps) {
      const {
        tabId, label, icon, disabled, closable,
      } = tabProps;
      return {
        tabId, label, icon, disabled, closable,
      };
    },
    shouldShowMover() {
      return (this.$el.offsetWidth < this.$refs.tabsCont.offsetWidth);
    },
    handleTabsContResize() {
      this.showMover = this.shouldShowMover();
    },
    onClickMover(dir) {
      const dist = 80 * (dir === 'backward' ? -1 : 1);
      const smallest = this.$refs.cont.offsetWidth - this.$refs.tabsCont.offsetWidth;
      this.tabsContLeft -= dist;
      if (this.tabsContLeft > 0) {
        this.tabsContLeft = 0;
      } else if (this.tabsContLeft < smallest) {
        this.tabsContLeft = smallest;
      }
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.showMover = this.shouldShowMover();
      coreAssistService.dom.onResize.on(this.$refs.tabsContSizeSensor, this.handleTabsContResize);
      coreAssistService.dom.onResize.on(this.$refs.contSizeSensor, this.handleTabsContResize);
    });
  },
  beforeDestroy() {
    coreAssistService.dom.onResize.off(this.$refs.tabsContSizeSensor, this.handleTabsContResize);
    coreAssistService.dom.onResize.off(this.$refs.contSizeSensor, this.handleTabsContResize);
  },
  components: {
    AcTab,
  },
};
</script>
