<template>
<li :class="['ac-tab', 'ac-typo-title', 'ac-layout', {
    'ac-tab--disabled': disabled,
    'ac-tab--active': active,
  }]"
  @click.prevent="onClickTab"
>
  <div :class="['ac-tab__inner', 'ac-layout',
    'ac-layout--center-items']"
  >
    <span :class="['ac-tab__inner__piece']" v-if="effectiveIcon">
      <ac-icon :type="effectiveIcon.type" :name="effectiveIcon.name" size="small" as-block></ac-icon>
    </span>
    <span :class="['ac-tab__inner__piece', 'ac-layout__flex',
      'ac-layout', 'ac-layout--center-justify']"
      v-if="label"
    >
      <span>{{ label }}</span>
    </span>
    <span :class="['ac-tab__inner__piece']" v-if="closable">
      <ac-icon class="ac-tab__inner__piece__close-mark"
        type="close"
        size="small"
        as-block
        @click.native.stop.prevent="onClickClose"
      ></ac-icon>
    </span>
  </div>
</li>
</template>

<script>

export default {
  name: 'AcTab',
  props: {
    tabId: {
      type: String,
      required: true,
    },
    label: {
      type: String,
    },
    /**
    ```js
    {
      type: string,
      name: string,
    }
    ```
     */
    icon: {
      type: [String, Object],
      default: null,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    closable: {
      type: Boolean,
      default: false,
    },
    active: {
      type: Boolean,
      default: false,
    },
  },
  inject: ['claimTabSelected', 'claimTabClose'],
  computed: {
    effectiveIcon() {
      if (!this.icon) { return null; }
      let iconObj = this.icon;
      if (typeof iconObj === 'string') {
        iconObj = { name: iconObj };
      }
      iconObj = Object.assign({
        type: 'iview',
        name: 'unknown',
      }, iconObj);
      return iconObj;
    },
  },
  methods: {
    onClickTab() {
      if (this.disabled) { return; }
      this.claimTabSelected(this.tabId);
    },
    onClickClose() {
      if (this.disabled) { return; }
      this.claimTabClose(this.tabId);
    },
  },
};
</script>
