<template>
  <div
    :class="['ac-table-toolbar__block', 'ac-layout', 'ac-layout--center-items', 'ac-layout-col']"
    :style="styles">
    <slot></slot>
  </div>
</template>

<script>
import { globalAssistService } from '$src/utils';

export default {
  name: 'AcTableToolbarBlock',
  props: {
    width: {
      type: [Number, String],
      default: 'auto',
    },
  },
  computed: {
    styles() {
      const style = {};
      style.width = globalAssistService.validate.hasCharacter(this.width.toString(), '%,px,auto')
        ? this.width
        : `${this.width}px`;
      return style;
    },
  },
};
</script>
