import { AcApp } from './components/ac-app';
import { AcNav, AcNavGroup, AcNavItem } from './components/ac-nav';
import { AcScrollView } from './components/ac-scroll-view';
import { AcAction, AcActionBlock, AcActionFilling } from './components/ac-action';
import { AcCode } from './components/ac-code';
import { AcDemo } from './components/ac-demo';
import { AcIcon, AcIconTip } from './components/ac-icon';
import { AcLoading } from './components/ac-loading';
import { AcTooltip } from './components/ac-tooltip';
import { AcToolbar, AcToolbarBlock, AcToolbarTitle, AcToolbarFilling } from './components/ac-toolbar';
import { AcTable } from './components/ac-table';
import { AcPanel, AcPanelGroup } from './components/ac-panel';
import { AcTabGroup, AcTabPage } from './components/ac-tab-layout';
import { AcButton } from './components/ac-button';
import { AcModal } from './components/ac-modal';
import { AcConfirm } from './components/ac-confirm';
import { AcEcharts } from './components/ac-echarts';
import { AcSprite, AcSpriteBlock, AcSpriteSeparator, AcSpriteIcon } from './components/ac-sprite';
import { AcTableToolbar, AcTableToolbarBlock, AcTableToolbarFilling } from './components/ac-table-toolbar';

const components = {
	AcApp,
	AcNav,
	AcNavGroup,
	AcNavItem,
	AcScrollView,
	AcAction,
	AcActionBlock,
	AcActionFilling,
	AcCode,
	AcDemo,
	AcIcon,
	AcIconTip,
	AcLoading,
	AcTooltip,
	AcToolbar,
	AcToolbarBlock,
	AcToolbarTitle,
	AcToolbarFilling,
	AcTable,
	AcPanel,
	AcPanelGroup,
	AcTabGroup,
	AcTabPage,
	AcButton,
	AcModal,
	AcConfirm,
	AcEcharts,
	AcSprite,
	AcSpriteBlock,
	AcSpriteSeparator,
	AcSpriteIcon,
	AcTableToolbar,
	AcTableToolbarBlock,
	AcTableToolbarFilling,
};

const AcComponent = {
	...components,
};

const install = function (Vue) {
	Object.keys(AcComponent).forEach(key => {
		Vue.component(key, AcComponent[key]);
	});

	Vue.prototype.$confirm = AcConfirm;
};

const API = {
	version: process.env.VERSION, // eslint-disable-line no-undef
	install,
	...components
};

export default API;
