<script>
/* eslint-disable no-console */
import AcPanelHeader from './ac-panel-header';
import AcPanelContent from './ac-panel-content';

/**
 * Events:
 * @event on-expanding `(expanding: boolean)`
 * @event select-tab `(newTabId: string, oldTabId: string)`
 */
export default {
  name: 'AcPanel',
  props: {
    panelId: {
      type: String,
    },
    collapsible: {
      type: Boolean,
      default: false,
    },
    initialCollapsed: {
      type: Boolean,
      default: false,
    },
    /**
     * Whether or not controlled `collapsed` state. Valid only if `collapsible === true`.
     * If false, the `collapsed` state will be managed by panel itself.
     * However once true, you are on your own for `accordion` behavior of this panel in
     * the wrapping group.
     */
    controlledCollapsed: {
      type: Boolean,
      default: false,
    },
    /** define the actual `collapsed` state if `collapsible === true && controlledCollapsed === true` */
    collapsed: {
      type: Boolean,
    },
    /** true if the content of panel should be loaded lazily and is currently not available,
     * hence the `on-expanding` event could be used to indicate if it is time to load or to
     * cancel the loading of the contents.
     */
    toLoadContent: {
      type: Boolean,
      default: false,
    },
    useTabs: {
      type: Boolean,
      default: false,
    },
    /** must define if `useTabs` */
    tabsConfig: {
      /** {default: string, tabsProps: [{tabId: string, label: string, disabled?: boolean, icon?: string}] } */
      type: Object,
    },
    /** Loading layer size */
    loadingHeight: Number,
    loadingWidth: Number,
  },
  inject: {
    acPanelGroupParam: { default: null },
    claimLastExpandedPanel: { default: null },
  },
  data() {
    return {
      internalCollapsed: this.controlledCollapsed ? this.collapsed : this.initialCollapsed,
      selectedTab: this.useTabs ? this.tabsConfig.default : null,
    };
  },
  computed: {
    isWithinGroup() {
      return (this.$parent && this.$parent.$options.name === 'AcPanelGroup');
    },
    isCollapsed() {
      if (!this.collapsible) {
        return false;
      } else if (this.controlledCollapsed) {
        /** it is controlled collapsed state, so bypass any internal calculation of collapsed state */
        return !!this.collapsed;
      } else if (this.isWithinGroup && this.acPanelGroupParam.accordion) {
        return (this.panelId !== this.acPanelGroupParam.lastExpandedPanel || this.internalCollapsed);
      }
      return this.internalCollapsed;
    },
  },
  methods: {
    onClickHeader(event) {
      event.stopPropagation();
      this.toggleCollapse();
    },
    onTabSelected(tabId) {
      this.selectedTab = tabId;
    },
    /**
     * Toggle the collapsed state. This method could be invoked publicly as an imperative
     * way to control the collapsed state of current panel.
     * @param {boolean} collapsing if not specified, the collapsed state is simply reverted.
     */
    toggleCollapse(collapsing) {
      if (!this.collapsible) { return; }
      if (this.controlledCollapsed) {
        /** it is controlled collapsed state, so ignore the input collapsing request and
         * force any internal calculation of collapsed state */
        this.internalCollapsed = this.collapsed;
      } else {
        let nCollapsing = collapsing;
        if (nCollapsing === undefined) {
          nCollapsing = !this.isCollapsed;
        }
        this.internalCollapsed = nCollapsing;
        if (!this.internalCollapsed && this.isWithinGroup) {
          this.claimLastExpandedPanel(this.panelId);
        }
      }

      this.$emit('on-expanding', !this.internalCollapsed);
    },
    hasSlotTitle() {
      return this.$slots.title !== undefined;
    },
    hasSlotAction() {
      return this.$slots.actions !== undefined;
    },
  },
  watch: {
    selectedTab(newTab, oldTab) {
      this.$emit('select-tab', newTab, oldTab);
    },
  },
  mounted() {
    this.$emit('on-expanding', !this.isCollapsed);
  },
  render() {
    let panelInnerNodes = null;

    if (!this.useTabs) {
      let panelHeader = null;
      let panelContentNodes = null;

      if (this.hasSlotTitle() || this.hasSlotAction()) {
        panelHeader = [
          <ac-panel-header
            collapsible={this.collapsible}
            collapsed={this.isCollapsed}
            {...{ props: this.$attrs }}
            nativeOnClick={this.onClickHeader}
          >
            {this.$slots.title}
            <template slot="actions">
              {this.$slots.actions}
            </template>
          </ac-panel-header>,
        ];
      }

      if (this.toLoadContent === true) {
        panelContentNodes = (
          <div
            style={{
              position: 'relative',
              height: this.loadingHeight ? `${this.loadingHeight}px` : 'auto',
              width: this.loadingWidth ? `${this.loadingWidth}px` : 'auto',
            }}
          >
            <ac-loading tip="加载中..." size="small" fix as-section-wrap></ac-loading>
          </div>
        );
      } else {
        panelContentNodes = this.$slots.default;
      }

      panelInnerNodes = [
        panelHeader,
        <ac-panel-content collapsed={this.isCollapsed} {...{ props: this.$attrs }}>
          {panelContentNodes}
        </ac-panel-content>,
      ];
    } else {
      /**
       * collect all tab contents from the slots, so we will be going through all slots
       * except `title` and `actions`.
       */
      const tabPanelNodes = [];

      this.tabsConfig.tabsProps.forEach((props) => {
        const slot = this.$slots[props.tabId];
        if (!slot) { return; }

        const node = (
          <ac-tab-page tabId={props.tabId}>
            {slot}
          </ac-tab-page>
        );

        tabPanelNodes.push(node);
      });

      panelInnerNodes = [
        <ac-tab-group class="ac-panel__tabs" tabsProps={this.tabsConfig.tabsProps}
          value={this.selectedTab} {...{ on: { 'on-select': this.onTabSelected } }}
        >
          {tabPanelNodes}
        </ac-tab-group>,
      ];
    }

    const rootNode = (
      <div
        class={[
          'ac-panel',
          { 'ac-panel--shadow': !this.isWithinGroup },
          { 'ac-panel--splitter': this.isWithinGroup },
        ]}
      >
        {panelInnerNodes}
      </div>
    );

    return rootNode;
  },
  components: {
    AcPanelHeader,
    AcPanelContent,
  },
};
</script>
