const GENERAL_COL_WIDTH = 100;
const INDEX_COL_WIDTH = 40;
const SELECTION_COL_WIDTH = 40;
const SELECTION_WITH_DROPDOWN_COL_WIDTH = 50;
const EXPAND_COL_WIDTH = 40;

function getNormalizedColWidth(colConfig) {
	if (colConfig.type) {
		if (colConfig.type === 'index') {
			return INDEX_COL_WIDTH;
		} else if (colConfig.type === 'selection') {
			if (colConfig.typeOptions) {
				return SELECTION_WITH_DROPDOWN_COL_WIDTH;
			}
			return SELECTION_COL_WIDTH;
		} else if (colConfig.type === 'expand') {
			return EXPAND_COL_WIDTH;
		}
		return GENERAL_COL_WIDTH;
	} else if (colConfig.width !== undefined) {
		if (typeof colConfig.width === 'string' && colConfig.width.indexOf('%') >= 0) {
			return colConfig.width;
		}
		/** treat all other cases as pixel */
		return parseInt(colConfig.width, 10);
	}
	return 'auto';
}

function getColStyles(colConfig) {
	const styles = {};
	const width = getNormalizedColWidth(colConfig);
	styles.width = (typeof width === 'number') ? `${width}px` : width;
	return styles;
}

function getColClasses(colConfig) {
	const classes = [];
	if (colConfig.type) {
		classes.push(`ac-table__col-${colConfig.type}`);
	}
	return classes.length > 0 ? classes : undefined;
}

function calcTableWidth(columnsConfig) {
	const candidates = [];
	let fixedWidth = 0;
	const percentageValues = [];

	columnsConfig.forEach((col) => {
		let width = getNormalizedColWidth(col);
		if (width === 'auto') {
			width = GENERAL_COL_WIDTH;
		}
		if (typeof width === 'number') {
			fixedWidth += width;
		} else {
			percentageValues.push(width);
		}
	});

	const totalPercent = percentageValues.reduce((acc, cur) => acc + parseInt(cur, 10), 0);
	fixedWidth = (fixedWidth * 100) / (100 - totalPercent);
	candidates.push(fixedWidth);

	percentageValues.forEach((p) => {
		const percent = parseInt(p, 10);
		const requiredWidth = (GENERAL_COL_WIDTH * 100) / (100 - percent);
		candidates.push(requiredWidth);
	});

	return candidates.reduce((max, cur) => Math.max(max, cur), -Infinity);
}

function calcColumnAdjustment({
	columnsConfig, targetCol, offset,
}) {
	/** for now, we only support flat columns */
	const isTargetColAuto = targetCol.width === 'auto' || targetCol.width === undefined;
	let hasAutoColAhead = false;
	let hasAutoColAfter = false;
	let foundTargetCol = false;
	let targetColIndex = -1;
	let nextCol = null;
	const adjustment = {};
	const minimumWidth = 16;

	columnsConfig.forEach((col, index) => {
		if (col.key === targetCol.key) {
			foundTargetCol = true;
			targetColIndex = index;
			return;
		}
		if (foundTargetCol && index === targetColIndex + 1) {
			nextCol = col;
		}
		if (col.width === 'auto' || col.width === undefined) {
			if (foundTargetCol) {
				hasAutoColAfter = true;
			} else {
				hasAutoColAhead = true;
			}
		}
	});

	if (isTargetColAuto) {
		if (nextCol) {
			adjustment[nextCol.key] = {
				width: Math.max(nextCol.width - offset, minimumWidth),
			};
		}
	} else if (hasAutoColAfter) {
		adjustment[targetCol.key] = {
			width: Math.max(targetCol.width + offset, minimumWidth),
		};
	} else if (hasAutoColAhead) {
		adjustment[nextCol.key] = {
			width: Math.max(nextCol.width - offset, minimumWidth),
		};
	}

	return adjustment;
}

export default {
	getColStyles,
	getColClasses,
	calcTableWidth,
	calcColumnAdjustment,
};
