<template>
<div :class="[`${cssPrefix}`]" :style="[resizerStyle, posStyle]"
  @mouseenter="onMouseEnter"
  @mouseleave="onMouseLeave"
  v-drag="{start, positioning, done}"
>
</div>
</template>

<script>
const drag = {
  inserted(el, binding) {
    const oDiv = el;
    const positioningFunc = binding.value.positioning;
    const doneFunc = binding.value.done;
    const startFunc = binding.value.start;
    oDiv.onmousedown = ev => {
      const disX = ev.clientX - oDiv.offsetLeft;
      const disY = ev.clientY - oDiv.offsetTop;
      document.onmousemove = _ev => {
        const l = Math.max(_ev.clientX - disX, 0);
        const t = Math.max(_ev.clientY - disY, 0);
        _ev.preventDefault();
        if (positioningFunc) {
          positioningFunc({
            left: l,
            top: t
          });
        }
      };
      document.onmouseup = () => {
        document.onmousemove = null;
        document.onmouseup = null;
        if (doneFunc) {
          doneFunc();
        }
      };
      if (startFunc) {
        startFunc();
      }
    };
  }
};

export default {
  name: "AcTableColResizer",
  props: {
    headerMetrics: {
      type: Object,
      required: true
    },
    headerProps: {
      type: Object,
      required: true
    },
    trigger: {
      type: Object,
      required: true
    }
  },
  inject: ["claimColResizerDone", "claimColResizerTriggerLock"],
  data() {
    return {
      cssPrefix: "ac-table-col-resizer",
      position: null,
      isResizing: false
    };
  },
  computed: {
    resizerStyle() {
      return {
        height: `${this.headerMetrics.clientHeight +
          this.headerProps.bodyMetrics.clientHeight}px`
      };
    },
    posStyle() {
      if (!this.position) {
        return {};
      }
      return {
        left: `${this.position.left}px`,
        top: `${this.position.top}px`
      };
    }
  },
  methods: {
    updatePosition() {
      const opRect = this.$el.offsetParent.getBoundingClientRect();
      this.position = {
        left: this.trigger.rect.left - opRect.left,
        top: this.trigger.rect.top - opRect.top
      };
    },
    onMouseEnter() {
      this.claimColResizerTriggerLock(true);
    },
    onMouseLeave() {
      if (this.isResizing) {
        return;
      }
      this.claimColResizerDone(0, false);
    },
    positioning(pos) {
      this.position.left = pos.left;
    },
    done() {
      const opRect = this.$el.offsetParent.getBoundingClientRect();
      const initLeft = this.trigger.rect.left - opRect.left;
      const offset = this.position.left - initLeft;

      this.isResizing = false;
      if (offset !== 0) {
        this.claimColResizerDone(offset, true);
      }
    },
    start() {
      this.isResizing = true;
    }
  },
  watch: {
    trigger() {
      this.updatePosition();
    }
  },
  mounted() {
    this.updatePosition();
  },
  components: {},
  directives: {
    drag
  }
};
</script>
