<script>
import { globalAssistService } from '$src/utils';
import AcLoadingAnimationDefault from './ac-loading-animation-default';

const LOADING_PLACEMENT_MAP = {
  center: ['ac-layout', 'ac-layout--center-items', 'ac-layout--center-justify'],
  'top-start': ['ac-layout', 'ac-layout--start-items', 'ac-layout--start-justify'],
  'top-center': ['ac-layout', 'ac-layout--center-items', 'ac-layout--start-justify'],
};

export default {
  name: 'AcLoading',
  data() {
    return {
      top: false,
      left: false,
    };
  },
  props: {
    tip: String,
    animation: {
      type: Number,
      default: 0,
      validator(value) {
        return globalAssistService.validate.oneOf(value, [0]);
      },
    },
    size: {
      type: String,
      default: 'primary',
      validator(value) {
        return globalAssistService.validate.oneOf(value, ['small', 'primary', 'large']);
      },
    },
    placement: {
      type: String,
      default: 'center',
      validator(value) {
        return globalAssistService.validate.oneOf(value, ['top-start', 'top-center', 'center']);
      },
    },
    cover: {
      type: Boolean,
      default: false,
    },
    fix: {
      type: Boolean,
      default: false,
    },
    asSectionWrap: {
      type: Boolean,
      default: true,
    },
  },
  computed: {
    classes() {
      return [
        'ac-loading',
        LOADING_PLACEMENT_MAP[this.placement],
        {
          'ac-loading--cover': this.cover,
          'ac-loading--fix': this.fix,
          'ac-loading--section-like': this.asSectionWrap,
        },
      ];
    },
  },
  render() {
    let animationNodes = null;

    if (this.animation === 0) {
      animationNodes = [<AcLoadingAnimationDefault size={this.size} tip={this.tip}/>];
    }

    const rootNode = <div class={this.classes}>{animationNodes}</div>;

    return rootNode;
  },
  components: {
    AcLoadingAnimationDefault,
  },
};
</script>
