<template>
  <div :class="['ac-table',
    'ac-layout',
    {'ac-table--border': useOuterBorder}]"
    :style="[wrapperStyles]"
  >
    <div :class="['ac-table__fixed', 'ac-layout__flex-none']" v-if="showFixedLeft">
      <div :class="['ac-table__fixed__left']">
        <ac-table-fixed-portion
          ref="fixedPortionLeft"
          side="left"
          :portion-columns-config="fixedColumnsConfig.left"
          :show-header="showHeader"
          :header-metrics="headerMetrics"
          :original-header-props="headerProps"
          :original-body-props="bodyProps"
        />
      </div>
    </div>
    <div :class="['ac-table__wrapper',
      'ac-layout__flex',
      {'ac-layout': isHeaderFixed},
      {'ac-layout--column': isHeaderFixed}]"
    >
      <ac-table-header
        ref="header"
        v-if="showHeader"
        v-bind="headerProps"
        :class="[{'ac-layout__flex-none': isHeaderFixed}]"
      ></ac-table-header>
      <ac-table-body
        ref="body"
        v-bind="bodyProps"
        :class="[{'ac-layout__flex': isHeaderFixed}]"
      ></ac-table-body>
      <ac-table-footer
        ref="footer"
        v-if="showFooter"
        v-bind="footerProps"
        :class="[{'ac-layout__flex-none': isHeaderFixed}]"
      ></ac-table-footer>
      <ac-table-pagination
        v-if="showPagination"
        v-bind="effectivePaginationConfig"
        :class="[{'ac-layout__flex-none': isHeaderFixed}]"
      ></ac-table-pagination>
      <div ref="sizeSensor" class="ac-table__size-sensor"></div>
    </div>
    <div :class="['ac-table__fixed', 'ac-layout__flex-none']" v-if="showFixedRight">
      <div :class="['ac-table__fixed__right']">
        <ac-table-fixed-portion
          ref="fixedPortionRight"
          side="right"
          :portion-columns-config="fixedColumnsConfig.right"
          :show-header="showHeader"
          :header-metrics="headerMetrics"
          :original-header-props="headerProps"
          :original-body-props="bodyProps"
        />
      </div>
    </div>
    <ac-table-col-resizer
      ref="colResizer"
      v-if="showResizer && nestLevel === 0"
      :header-metrics="headerMetrics"
      :header-props="headerProps"
      :trigger="resizerTrigger"
    />
  </div>
</template>

<script>
import { globalAssistService } from '$src/utils';
import AcTableHeader from './ac-table-header';
import AcTableBody from './ac-table-body';
import AcTableFooter from './ac-table-footer';
import AcTablePagination from './ac-table-pagination';
import AcTableFixedPortion from './ac-table-fixed-portion';
import AcTableColResizer from './ac-table-col-resizer';
import utilService from './util.service';

/**
 * Events:
 * @event request-sort `({colKey: string, cancel?: boolean, ascending?: boolean})`
 * @event request-column-adjust `({ [key: string]: {width: number|string} })`
 * @event on-entry-expanding `({entry: Entry, expanding: boolean})`
 * @event on-entry-selecting `({entry: Entry, selecting: boolean})`
 * @event on-change-page-number `({ pageNum: number })`
 */
export default {
  name: 'AcTable',
  props: {
    /** Indicate if this table is shown by parent on page or not, instead of showing or hiding the table */
    visible: {
      type: Boolean,
      default: true,
    },
    /**
     * Array to define the various table columns.
    ```js
    {
      key: string,
      type?: string, // index, selection, expand
      typeOptions?: {// effective only when type === selection
        selectedOperationOptions: {
          text: string,
          click: () => {}
        }[],
        forEveryRow?: boolean,
      },
      dataKey?: string,
      title?: string,
      width?: number|string, // only supports px and %
      render?: (h, {entry, index}) => VNode, // the `index` is not the entry index in original data array, but just index in the display order of table body.
      sorter?: (a, b) => boolean,
      children?: ColumnConfig[],
      fixed?: boolean,
    }[]
    ```
     */
    columnsConfig: {
      type: Array,
      required: true,
    },
    /**
    ```js
    {
      _key: string,
      _children?: Entry[],
      _toLoadChildren?: boolean,// indicate if the `_children` array need to be initially lazily loaded.
                                // Or if there is no children array for this entry, but table row is expandible,
                                // this indicate if the expanded contents need to be lazy loaded.
                                // If `true`, loading piece will be shown in the expanded area.
      _childTotal?: number, // total number of children entries. Useful when you want to do bufferring loading along with pagination for nested tables.
      [name: string]: any,
    }[]
    ```
     */
    data: {
      type: Array,
      required: true,
    },
    columnAdjustable: {
      type: Boolean,
      default: false,
    },
    /** accordion behavior for expandable rows, hence only effective when there are expandable rows */
    accordion: {
      type: Boolean,
      default: false,
    },
    sortedColumns: {
      /** {colKey: string, ascending: boolean}[] */
      /** for now, only support single sorted column */
      type: Array,
      default() {
        return [];
      },
    },
    /** small, medium, large */
    size: {
      type: String,
      default: 'medium',
    },
    stripe: {
      type: Boolean,
      default: true,
    },
    border: {
      type: Boolean,
      default: false,
    },
    footerConfig: {
      /** {render?: Function, colFooterRenders?: {[colKey: string]: Function}} */
      type: Object,
      default: null,
    },
    paginationConfig: {
      type: Object,
      default: null,
    },
    showHeader: {
      type: Boolean,
      default: true,
    },
    width: {
      type: [Number, String],
      default: 'auto',
    },
    height: {
      type: [Number, String],
      default: 'auto',
    },
    nestLevel: {
      type: Number,
      default: 0,
    },
    /** valid only when nested === true */
    indentSize: {
      type: Number,
      default: 16,
    },
  },
  provide() {
    return {
      claimEntrySelected: this.claimEntrySelected,
      claimSelectAllEntries: this.claimSelectAllEntries,
      claimEntryExpanded: this.claimEntryExpanded,
      claimBodyMetrics: this.claimBodyMetrics,
      claimSortRequest: this.claimSortRequest,
      claimChangePagination: this.claimChangePagination,
      claimNestedEntryExpanded: this.claimNestedEntryExpanded,
      claimColResizerTrigger: this.claimColResizerTrigger,
      claimColResizerDone: this.claimColResizerDone,
      claimColResizerTriggerLock: this.claimColResizerTriggerLock,
    };
  },
  data() {
    return {
      selectedSet: new Set(),
      shadowSelectedArr: [],
      expandedSet: new Set(),
      shadowExpandedArr: [],
      bodyMetrics: {
        left: null,
        top: null,
        width: null,
        clientWidth: null,
        height: null,
        clientHeight: null,
      },
      headerMetrics: {
        clientHeight: null,
      },
      headerObserver: null,
      /** valid only when showPagination === true */
      pagination: {
        pageNum: undefined,
        pageSize: undefined,
      },
      showResizer: false,
      resizerTrigger: null,
      isTriggerLocked: false,
    };
  },
  computed: {
    /** {colspan: number, rowspan, col: ColumnConfig}[][] */
    layeredColumnsConfig() {
      function getLayersFromColumnConfig(col) {
        const layers = [];
        const childrenLayers = [];

        if (!Array.isArray(col.children)) {
          return [[{ colspan: 1, rowspan: 1, col }]];
        }

        col.children.forEach((childCol) => {
          childrenLayers.push(getLayersFromColumnConfig(childCol));
        });

        const maxLayerNum = childrenLayers.reduce((max, curLayers) => (Math.max(max, curLayers.length)), 0);
        const totalColspan = childrenLayers.reduce((acc, curLayers) => (acc + curLayers[0][0].colspan), 0);

        childrenLayers.reduce((lyrs, curLayers) => {
          const toLayers = lyrs;

          if (maxLayerNum > curLayers.length) {
            /** adjust the rowspan of elements of each layer */
            const diff = maxLayerNum - curLayers.length;
            curLayers.forEach((layer) => {
              layer.forEach((le) => {
                if (Array.isArray(le.col.children)) { return; }
                const lee = le;
                lee.rowspan += diff;
              });
            });
          }

          for (let i = 0; i < maxLayerNum; ++i) {
            if (!Array.isArray(toLayers[i])) {
              toLayers[i] = [];
            }
            if (Array.isArray(curLayers[i])) {
              toLayers[i] = toLayers[i].concat(curLayers[i]);
            }
          }

          return lyrs;
        }, layers);

        layers.unshift([{ colspan: totalColspan, rowspan: 1, col }]);

        return layers;
      }

      const topLayers = getLayersFromColumnConfig({
        children: this.columnsConfig,
      });

      return topLayers.slice(1);
    },
    /** columns that are as leaves hence have no children */
    effectiveColumnsConfig() {
      function getLeaves(col) {
        const leaves = [];
        if (!Array.isArray(col.children)) {
          return [col];
        }
        col.children.forEach((childCol) => {
          Array.prototype.push.apply(leaves, getLeaves(childCol));
        });
        return leaves;
      }

      return getLeaves({
        children: this.columnsConfig,
      });
    },
    fixedColumnsConfig() {
      const fixedCols = {
        left: null,
        right: null,
      };
      let i = 0;

      for (i = 0; i < this.columnsConfig.length; ++i) {
        if (this.columnsConfig[i].fixed !== true) { break; }
        /** fixed column must have width specified explicitly */
        if (!this.columnsConfig[i].type && typeof this.columnsConfig[i].width !== 'number') {
          throw new Error('width for fixed column is not specified properly');
        }
        if (fixedCols.left) {
          fixedCols.left.push(this.columnsConfig[i]);
        } else {
          fixedCols.left = [this.columnsConfig[i]];
        }
      }

      for (i = this.columnsConfig.length - 1; i >= 0; --i) {
        if (this.columnsConfig[i].fixed !== true) { break; }
        /** fixed column must have width specified explicitly */
        if (!this.columnsConfig[i].type && typeof this.columnsConfig[i].width !== 'number') {
          throw new Error('width for fixed column is not specified properly');
        }
        if (fixedCols.right) {
          fixedCols.right.unshift(this.columnsConfig[i]);
        } else {
          fixedCols.right = [this.columnsConfig[i]];
        }
      }

      return fixedCols;
    },
    /** Sorted copy of original data array */
    sortedData() {
      const sortedData = this.data.slice(0);
      /** for now, support single sorted column */
      if (this.sortedColumns.length > 0) {
        const sortCol = this.effectiveColumnsConfig.find(col => (col.key === this.sortedColumns[0].colKey));
        if (sortCol && typeof sortCol.sorter === 'function') {
          sortedData.sort(sortCol.sorter);
          if (!this.sortedColumns[0].ascending) {
            sortedData.reverse();
          }
        } else if (sortCol && typeof sortCol.sorter === 'boolean') {
          return sortedData;
        }
      }
      return sortedData;
    },
    /** paged copy of sorted data array */
    pagedData() {
      if (!this.showPagination) { return null; }
      let pagedData = this.sortedData.slice(0);
      let start = 0;
      let end = pagedData.length;

      start = Math.max(this.pagination.pageNum - 1, 0) * this.pagination.pageSize;
      end = start + this.pagination.pageSize;

      pagedData = pagedData.slice(start, end);

      return pagedData;
    },
    /** Either paged copy, or just sorted copy */
    effectiveData() {
      if (this.showPagination) {
        return this.pagedData;
      }
      return this.sortedData;
    },
    selectedStatus() {
      if (this.shadowSelectedArr.length >= this.data.length) {
        return 'all';
      } else if (this.shadowSelectedArr.length > 0) {
        return 'partial';
      }
      return 'none';
    },
    headerProps() {
      const {
        columnsConfig, sortedColumns, size, border,
      } = this.$props;
      return {
        columnsConfig,
        layeredColumnsConfig: this.layeredColumnsConfig,
        effectiveColumnsConfig: this.effectiveColumnsConfig,
        columnAdjustable: this.effectiveColumnAdjustable,
        sortedColumns,
        size,
        border,
        bodyMetrics: this.bodyMetrics,
        selectedStatus: this.selectedStatus,
      };
    },
    bodyProps() {
      const {
        columnsConfig, sortedColumns, size, stripe, border, nestLevel, indentSize, accordion,
      } = this.$props;
      return {
        columnsConfig,
        effectiveColumnsConfig: this.effectiveColumnsConfig,
        data: this.effectiveData,
        accordion,
        sortedColumns,
        size,
        stripe,
        border,
        nestLevel,
        indentSize,
        selectedArr: this.shadowSelectedArr,
        expandedArr: this.shadowExpandedArr,
        pagination: this.showPagination ? this.pagination : null,
        effectivePaginationConfig: this.effectivePaginationConfig,
        bodyMetrics: this.bodyMetrics,
      };
    },
    footerProps() {
      const {
        columnsConfig, size, border,
      } = this.$props;
      return {
        columnsConfig,
        effectiveColumnsConfig: this.effectiveColumnsConfig,
        size,
        border,
        bodyMetrics: this.bodyMetrics,
      };
    },
    showFixedLeft() {
      return !!this.fixedColumnsConfig.left;
    },
    showFixedRight() {
      return !!this.fixedColumnsConfig.right;
    },
    showFooter() {
      return this.footerConfig !== null;
    },
    showPagination() {
      return this.paginationConfig !== null;
    },
    /** make up every default value for pagination config */
    effectivePaginationConfig() {
      if (!this.showPagination) {
        return null;
      }
      const mergedConfig = Object.assign({
        current: 1,
        total: 0,
        pageSize: 10,
        pageSizeOpts: [10, 20, 30, 40],
        placement: 'top',
      }, this.paginationConfig);

      if (mergedConfig.nestedPagination !== null) {
        mergedConfig.nestedPagination = Object.assign({
          current: 1,
          total: 0, // should be updated per nested table
          pageSize: 10,
          pageSizeOpts: [10, 20, 30, 40],
          placement: 'top',
        }, mergedConfig.nestedPagination);
      }

      return mergedConfig;
    },
    effectiveWidth() {
      if (this.nestLevel > 0) {
        return 'auto';
      }
      return this.width;
    },
    /** nested table should not have scrollbar for the table body */
    effectiveHeight() {
      if (this.nestLevel > 0) {
        return 'auto';
      }
      return this.height;
    },
    effectiveColumnAdjustable() {
      /** we don't support adjustable column for grouped header */
      return (this.columnAdjustable && this.layeredColumnsConfig.length <= 1);
    },
    isHeaderFixed() {
      return this.effectiveHeight !== 'auto';
    },
    useOuterBorder() {
      return this.nestLevel === 0 && this.border;
    },
    wrapperStyles() {
      const styles = {
        height: (typeof this.effectiveHeight === 'number') ? `${this.effectiveHeight}px` : this.effectiveHeight,
        width: (typeof this.effectiveWidth === 'number') ? `${this.effectiveWidth}px` : this.effectiveWidth,
      };
      return styles;
    },
  },
  watch: {
    visible(val) {
      if (val) {
        this.$nextTick(() => {
          this.reinit();
        });
      }
    },
    effectivePaginationConfig: {
      handler(val, oVal) {
        /** only initialize pagination data when we are creating the pagination component */
        if (val && !oVal) {
          this.pagination.pageNum = val.current;
          this.pagination.pageSize = val.pageSize;
        }
        if (val && oVal && val.current !== oVal.current) {
          this.pagination.pageNum = val.current;
        }
      },
      immediate: true,
    },
  },
  methods: {
    /**
     * @public
     * should be called whenever the table is shown from hidden, or anytime applicable
     */
    reinit() {
      if (this.$refs.body) {
        this.$refs.body.reinit();
      }
    },
    claimEntrySelected(entry, selected) {
      if (selected) {
        this.selectedSet.add(entry._key);
      } else {
        this.selectedSet.delete(entry._key);
      }
      this.shadowSelectedArr = Array.from(this.selectedSet);
      this.$emit('on-entry-selecting', { entry, selecting: selected });
    },
    claimNestedEntrySelected(params) {
      /** simply bubble up this event */
      this.$emit('on-entry-selecting', params);
    },
    claimSelectAllEntries(selectAll) {
      if (!selectAll) {
        this.selectedSet.clear();
      } else {
        this.data.forEach((entry) => {
          this.selectedSet.add(entry._key);
        });
      }
      this.shadowSelectedArr = Array.from(this.selectedSet);
      this.$emit('on-all-selecting', selectAll);
    },
    /** @public */
    claimEntryExpanded(entry, expanded) {
      if (expanded) {
        if (this.accordion) {
          this.expandedSet.clear();
        }
        this.expandedSet.add(entry._key);
      } else {
        this.expandedSet.delete(entry._key);
      }
      this.shadowExpandedArr = Array.from(this.expandedSet);
      this.$emit('on-entry-expanding', { entry, expanding: expanded });
    },
    claimNestedEntryExpanded(params) {
      /** simply bubble up this event */
      this.$emit('on-entry-expanding', params);
    },
    claimBodyMetrics({
      left, top, width, clientWidth, height, clientHeight,
    }) {
      if (left !== undefined) {
        if (left !== this.bodyMetrics.left) {
          /** for performance, have to do this synchronously instead of wait for reactive watcher in the header component */
          if (this.$refs.header) {
            this.$refs.header.updatePosition({ left });
          }
        }
        this.bodyMetrics.left = left;
      }
      if (top !== undefined) {
        if (top !== this.bodyMetrics.top) {
          /** again for performance */
          if (this.$refs.fixedPortionLeft) { this.$refs.fixedPortionLeft.updateBodyPosition({ top }); }
          if (this.$refs.fixedPortionRight) { this.$refs.fixedPortionRight.updateBodyPosition({ top }); }
          this.$refs.body.updatePosition({ top });
        }
        this.bodyMetrics.top = top;
      }
      if (width !== undefined) { this.bodyMetrics.width = width; }
      if (clientWidth !== undefined) { this.bodyMetrics.clientWidth = clientWidth; }
      if (height !== undefined) { this.bodyMetrics.height = height; }
      if (clientHeight !== undefined) { this.bodyMetrics.clientHeight = clientHeight; }
    },
    claimSortRequest(colKey, type) {
      const request = { colKey };
      if (type === 'cancel') {
        request.cancel = true;
      } else {
        request.ascending = (type === 'asc');
      }
      this.$emit('request-sort', request);
    },
    claimChangePagination(type, value) {
      if (type === 'page-number') {
        this.pagination.pageNum = value;
        this.$emit('on-change-page-number', { pageNum: value });
      } else if (type === 'page-size') {
        this.pagination.pageSize = value;
      }
    },
    claimColResizerTrigger({
      rect, col, colEl, relatedTarget,
    }) {
      if (this.isTriggerLocked) { return; }
      if (rect === null && relatedTarget && relatedTarget === this.$refs.colResizer.$el) { return; }
      this.showResizer = !!rect;
      this.resizerTrigger = {
        rect: rect ? {
          left: rect.left,
          right: rect.right,
          top: rect.top,
          bottom: rect.bottom,
          width: rect.width,
          height: rect.height,
        } : null,
        col,
        colEl,
      };
    },
    claimColResizerTriggerLock(locked) {
      this.isTriggerLocked = locked;
    },
    claimColResizerDone(adjustedOffset, doResize) {
      this.isTriggerLocked = false;
      this.showResizer = false;
      if (doResize) {
        if (!this.resizerTrigger || !this.resizerTrigger.col || !adjustedOffset) { return; }
        this.$emit(
          'request-column-adjust',
          utilService.calcColumnAdjustment({
            columnsConfig: this.columnsConfig,
            targetCol: this.resizerTrigger.col,
            targetColEl: this.resizerTrigger.colEl,
            offset: adjustedOffset,
          }),
        );
      }
    },
    handleTableResize() {
      this.$refs.body.shortDelayReportBodyMetrics();
    },
    updateHeaderMetrics() {
      const headerRef = this.$refs.header;
      Object.assign(this.headerMetrics, {
        clientHeight: headerRef ? headerRef.$el.clientHeight : 0,
      });
    },
    /** @public when the row expanded, it will be log in 'shadowExpandedArr',
     * then next time we reload the tableData, the row expanded will expand again,
     * if you don't want to expand again, you can imperative clear it.
     * */
    clearExpandedLog() {
      this.shadowExpandedArr = [];
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.updateHeaderMetrics();
      globalAssistService.dom.onResize.on(this.$refs.sizeSensor, this.handleTableResize);
      if (this.$refs.header) {
        this.headerObserver = globalAssistService.dom.setupMutationObserver(this.$refs.header.$el, null, this.updateHeaderMetrics);
      }
    });
  },
  beforeDestroy() {
    globalAssistService.dom.onResize.off(this.$refs.sizeSensor, this.handleTableResize);
    if (this.headerObserver) {
      this.headerObserver.disconnect();
    }
  },
  components: {
    AcTableHeader,
    AcTableBody,
    AcTableFooter,
    AcTablePagination,
    AcTableFixedPortion,
    AcTableColResizer,
  },
};
</script>
